/*
*   Name: Bizagi Database Plugin
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This script creates a plugin to handler all db functionalities
*   -   Pre-requisite pouchDB
*/
bizagi.workportal.services.db.extend("bizagi.workportal.services.db", {
  // STATIC METHODS
},
{
  // INSTANCE METHODS
  /**
   * 
   * @param {*} params 
   */
  getTaskSyncError: function getTaskSyncError(params) {
    var offlineId = params.idCase + "-" + params.idWorkitem;
    return this.getTask(offlineId).
    then(function (taskObject) {
      if (!bizagi.util.isEmpty(taskObject) && !bizagi.util.isEmpty(taskObject.syncDetails)) {
        return $.Deferred().resolve(taskObject.syncDetails).promise();
      }
      return $.Deferred().resolve(undefined).promise();
    },
    function () {
      //Error - 404
      return $.Deferred().resolve(undefined).promise();
    }).

    then(function (syncError) {
      if (params.deferred) {
        params.deferred.resolve(syncError);
      }
      return syncError;
    });
  },

  getInboxSyncErrors: function getInboxSyncErrors() {
    return this.dbActivities.allDocs({ include_docs: true }).toJqueryPromise().
    then(function (response) {
      var casesMap = {};
      var casesList = $.grep(response.rows, function (item) {
        return !bizagi.util.isEmpty(item.doc.syncStatus) && !bizagi.util.isEmpty(item.doc.syncDetails);
      });

      casesList.forEach(function (item) {
        var doc = item.doc;
        casesMap[item.id] = doc.syncDetails;
      });
      return casesMap;
    }).
    fail(function (err) {
      bizagi.util.console.error("Error getting inbox sync errors", err);
      return err;
    });
  },

  /**
      * Returns all the cases with sync errors
      */
  getSyncErrorsList: function getSyncErrorsList() {
    var errors = [];
    var self = this;

    return self.dbStartData.allDocs({
      include_docs: true,
      attachments: false }).
    toJqueryPromise().
    then(function (startCasesResponse) {
      var startErrors = $.grep(startCasesResponse.rows, function (item) {
        return !bizagi.util.isEmpty(item.doc.syncStatus) && !bizagi.util.isEmpty(item.doc.syncDetails);
      });
      errors = errors.concat(startErrors.map(function (item) {
        item = item.doc;
        item.offlineType = "start";
        return self.serializeError(item);
      }));
      return self.dbActivities.allDocs({
        include_docs: true,
        attachments: false }).
      toJqueryPromise();
    }).
    then(function (tasksCasesResponse) {
      var tasksErrors = $.grep(tasksCasesResponse.rows, function (item) {
        return !bizagi.util.isEmpty(item.doc.syncStatus) && !bizagi.util.isEmpty(item.doc.syncDetails);
      });
      errors = errors.concat(tasksErrors.map(function (item) {
        item = item.doc;
        item.offlineType = "task";
        return self.serializeError(item);
      }));
      return errors;
    }).
    fail(function (err) {
      bizagi.log("Error loading data before syncronization");
      return err;
    });
  },

  /**
      * Serialize the error in order to unify the response between tables
      * @param {} item The start case or activity to serialize
      */
  serializeError: function serializeError(item) {
    return {
      radnumber: item.radNumber,
      idcase: item.idCase,
      idworkitem: item.workItems[0].idWorkItem,
      errortype: item.syncDetails.type,
      synctimestamp: item.syncDetails.timestamp,
      activity: item.offlineType === "task" ? item.workItems[0].TaskName : undefined,
      activityduedate: item.offlineType === "task" ? item.endDate : undefined,
      process: item.process,
      offlinetype: item.offlineType === "task" ? "task" : "start",
      isdraft: !(item.offlineType === "task"),
      syncerror: item.syncDetails,
      state: item.offlineType === "task" ? item.state : undefined };

  } });