/**
 *   Name:   Bizagi diagnostics service
 *   Author: Bryan Alejandro Cubillos Prieto
 *   Date:   09/21/2020
 *   Comments:
 *   =>      This is a new service for Bizagi diagnostics observer, required for centralized behavior
 */

bizagi.workportal.services.service.extend("bizagi.workportal.services.service", {

  /*************************************************************************************************
                                                                                   *          COMPONENT LOCAL DATA
                                                                                   *************************************************************************************************/
  DIAGNOSTICS_OBSERVER_COMPONENT_NAME: 'bizagi.workportal.services.service.diagnostics.observer',
  DIAGNOSTICS_OBSERVER_ELEMENT_OBSERVED: document.body,
  DIAGNOSTICS_OBSERVER_MAX_EVENTS_IN_QUEUE: 10,
  DIAGNOSTICS_OBSERVER_MAX_PATH_EVENT: 5,
  DIAGNOSTICS_OBSERVER_OBSERVER_OPTIONS: {
    attributes: true,
    childList: true,
    characterData: true,
    subtree: true },

  DIAGNOSTICS_OBSERVER_MAX_SIZE_HTML_EVENT_MB: 1,
  DIAGNOSTICS_OBSERVER_COMPONENT_TYPES: {
    FORM: 'form',
    DISCUSSIONS: 'discussions',
    ACTION_SHEET: 'action-sheet',
    FILES: 'files',
    MAIN_MENU: 'main-menu',
    SUMMARY_CASE: 'summary-case' } },

{
  /*************************************************************************************************
   *          CONSTRUCTOR
   *************************************************************************************************/
  init: function init(params) {

    // Call base
    var self = this;
    self._super(params);

    // flag
    self.diagnosticsServiceEnabled = bizagi.util.isMobileErrorTracesEnabled();

    if (self.diagnosticsServiceEnabled) {

      // set default variables
      self.setDiagnosticsObserverDefaultData();

      // Behavior
      self.configureDiagnosticsObserverHandlers();
    }
  },

  /*************************************************************************************************
      *          LIFE CYCLE
      *************************************************************************************************/
  setDiagnosticsObserverDefaultData: function setDiagnosticsObserverDefaultData() {
    var self = this;
    self.diagnosticsObserverEventsQueue = [];
    self.MutationObserver = window.MutationObserver || window.WebKitMutationObserver || window.MozMutationObserver;
  },

  /*************************************************************************************************
      *          SERVICE METHODS
      *************************************************************************************************/
  configureDiagnosticsObserverHandlers: function configureDiagnosticsObserverHandlers() {
    var self = this;

    // Is required to get the first avents before DOM is ready
    self.setTouchEndObserver();

    // Observer
    self.eventsObserver();
  },

  setTouchEndObserver: function setTouchEndObserver() {
    var self = this;
    window.ontouchend = null;
    window.ontouchend = function (element) {
      self.addDiagnosticsObsercerEvent(self.getEventObject(element));
    };
  },

  eventsObserver: function eventsObserver() {
    var self = this;

    $(self.Class.DIAGNOSTICS_OBSERVER_ELEMENT_OBSERVED).ready(function () {
      var observer = new self.MutationObserver(function () {
        self.setTouchEndObserver();
      });
      observer.observe(self.Class.DIAGNOSTICS_OBSERVER_ELEMENT_OBSERVED, self.Class.DIAGNOSTICS_OBSERVER_OBSERVER_OPTIONS);
    });
  },

  getQueueListEvents: function getQueueListEvents() {
    var self = this;
    return self.diagnosticsObserverEventsQueue;
  },

  /*************************************************************************************************
      *          SERVICE FUNCTIONS
      *************************************************************************************************/
  getEventObject: function getEventObject(element) {
    var self = this;
    return {
      type: element.type,
      componentType: element.composedPath ? self.getDiagnosticsElementComponentType(element.composedPath()) : undefined,
      dateTime: bizagi.util.getCurrentDatetime(),
      srcElement: element.composedPath ? self.getDiagnosticsElementPath(element.composedPath()) : undefined };

  },

  addDiagnosticsObsercerEvent: function addDiagnosticsObsercerEvent(element) {
    var self = this;
    if (self.diagnosticsObserverEventsQueue.length >= self.Class.DIAGNOSTICS_OBSERVER_MAX_EVENTS_IN_QUEUE) {
      self.diagnosticsObserverEventsQueue.shift();
    }
    self.diagnosticsObserverEventsQueue.push(element);
  },

  getDataservice: function getDataservice() {
    return bizagiWorkportal.deviceFactory.workportal.dataService;
  },

  getDiagnosticsElementPath: function getDiagnosticsElementPath(sourcePath) {
    var self = this;
    var pathList = [];

    for (j = 0; j < sourcePath.length && j < self.Class.DIAGNOSTICS_OBSERVER_MAX_PATH_EVENT; j++) {

      var parent = $(sourcePath[j]);

      var parentProperties = {
        id: parent.prop("id"),
        "class": parent.prop("class"),
        text: parent.prop("innerText") };


      pathList.push(parentProperties);
    }

    // This is a hack for size html events
    var sizeEventHtml = bizagi.util.getSizeJsonObjectInMegaBytes(pathList);

    return sizeEventHtml < self.Class.DIAGNOSTICS_OBSERVER_MAX_SIZE_HTML_EVENT_MB ? pathList : [];
  },

  getDiagnosticsElementComponentType: function getDiagnosticsElementComponentType(sourcePath) {
    var self = this;
    var validateElement = self.validateDiagnosticsIfContainsElement;

    for (j = 0; j < sourcePath.length; j++) {
      var parent = $(sourcePath[j]);

      var type = self.getDiagnosticsElementComponentTypeByClass(parent);
      if (type) {
        return type;
      }

      type = self.getDiagnosticsElementComponentTypeById(parent);
      if (type) {
        return type;
      }

      if (self.validateDiagnosticsKendoView(parent) && validateElement('>div.km-content', parent)) {
        if (validateElement('>div.km-content >div.bz-form', parent)) {
          return self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.FORM;
        } else if (validateElement('>div.km-content div.bz-wp-box-discussion', parent) || validateElement('>div.km-content div.ui-bz-wp-discussions-box-main', parent)) {
          return self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.DISCUSSIONS;
        } else if (validateElement('>div.km-content div.bz-wp-box-files', parent)) {
          return self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.FILES;
        }
      }
    }

    return bizagi.util.getLastProcessInboxInputTray();
  },

  getDiagnosticsElementComponentTypeByClass: function getDiagnosticsElementComponentTypeByClass(parent) {
    var self = this;
    var type = undefined;

    if (parent.hasClass("bz-form")) {
      type = self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.FORM;
    } else if (parent.hasClass("summary-case")) {
      type = self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.SUMMARY_CASE;
    } else if (parent.hasClass("bz-action-sheet")) {
      type = self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.ACTION_SHEET;
    } else if (parent.hasClass("bz-wp-box-discussion")) {
      type = self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.DISCUSSIONS;
    } else if (parent.hasClass("bz-wp-box-files")) {
      type = self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.FILES;
    } else if (parent.hasClass("menu-content")) {
      type = self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.MAIN_MENU;
    }
    return type;
  },

  getDiagnosticsElementComponentTypeById: function getDiagnosticsElementComponentTypeById(parent) {
    var self = this;
    var type = undefined;

    if (parent.attr('id') == 'summary-case') {
      type = self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.SUMMARY_CASE;
    } else if (parent.attr('id') == 'menu-content') {
      type = self.Class.DIAGNOSTICS_OBSERVER_COMPONENT_TYPES.MAIN_MENU;
    }
    return type;
  },

  validateDiagnosticsKendoView: function validateDiagnosticsKendoView(parent) {
    return parent.hasClass("km-view") && parent.attr("data-role") == 'view';
  },

  validateDiagnosticsIfContainsElement: function validateDiagnosticsIfContainsElement(element, parent) {
    return $(element, parent).length > 0;
  } });