/**
 * Extension for bizagi.workportal.diagnostics.db
 * All the functionalities related with diagnostics errors in db
 */

bizagi.workportal.diagnostics.db.extend("bizagi.workportal.diagnostics.db", {}, {

  /**
                                                                                   * Save an error with their correct id
                                                                                   * @param {*} id 
                                                                                   * @param {*} error 
                                                                                   */
  saveError: function saveError(id, error) {
    try {
      error = JSON.parse(JSON.stringify(error));
      error._id = "" + id;
      return bizagi.util.db.saveDocument(this.dbErrors, error);
    }
    catch (e) {
      bizagi.util.console.error(this.Class.fullName + ": Error parsing the error object", e, error);
    }

  },

  /**
      * Get an error by their id
      * @param {} id 
      */
  getError: function getError(id) {
    return bizagi.util.db.getDocument(this.dbErrors, "" + id);
  },

  getErrorsList: function getErrorsList(maxErrors) {
    var getErrorsQuery = {
      map: function map(error) {
        if (error.timestamp) {
          emit(error.timestamp);
        }
      } };


    var queryArgs = {
      include_docs: true,
      limit: maxErrors,
      conflicts: false,
      descending: false };


    return bizagi.util.db.executeQuery(this.dbErrors, getErrorsQuery, queryArgs).
    then(function (queryRes) {
      return queryRes.rows.map(function (row) {
        if (row.doc) {
          delete row.doc._id;
          delete row.doc._rev;
        }
        return row.doc;
      });
    });
  },

  /**
      * Delete an error by their id
      * @param {*} id 
      */
  deleteError: function deleteError(id) {
    return bizagi.util.db.deleteDocument(this.dbErrors, "" + id);
  } });