/*
*   Name: Bizagi Rendering Services
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This class will provide a facade to access to workportal REST services
*/
bizagi.sync = bizagi.sync || {};
bizagi.sync.services = bizagi.sync.services || {};
bizagi.sync.services.service = bizagi.sync.services.service || {};
$.Class.extend("bizagi.sync.services.service", {
  // STATIC METHODS
  logKey: "OFFLINE SYNC TRACES::",
  syncTraces: {},

  getCurrentSyncStep: function getCurrentSyncStep(id) {
    return this.syncTraces[id] ? this.syncTraces[id].currentStep : null;
  },

  setCurrentSyncStep: function setCurrentSyncStep(id, step, stepParameters) {
    var trace = this.syncTraces[id] || {};
    var traceAlreadyAdded = !bizagi.util.isEmpty(trace);
    trace.currentStep = step;
    trace.steps = trace.steps || [];
    trace.steps.push({
      step: step,
      stepParameters: stepParameters });


    if (!traceAlreadyAdded) {
      this.syncTraces[id] = trace;
    }
  },

  addSyncStepResponse: function addSyncStepResponse(id, stepName, stepResponse) {
    var trace = this.syncTraces[id];
    if (bizagi.util.isEmpty(trace)) {
      console.error("OFFLINE SYNC TRACES::addCurrentSyncStepResponse:: Sync trace does not exist", id);
      return;
    }
    if (trace.steps == undefined) {
      trace.steps = [];
    }
    var stepObject = trace.steps.find(function (value) {
      return value.step == stepName;
    });
    if (bizagi.util.isEmpty(stepObject)) {
      console.warn("OFFLINE SYNC TRACES::addCurrentSyncStepResponse:: step " + stepName + " does not exist", stepName);
      stepObject = {
        step: "not-found-" + bizagi.util.randomNumber() + "-" + stepName };

      trace.steps.push(stepObject);
    }
    stepObject.stepResponse = stepResponse;
  },

  addCurrentSyncStepResponse: function addCurrentSyncStepResponse(id, stepResponse) {
    var trace = this.syncTraces[id];
    if (bizagi.util.isEmpty(trace)) {
      console.error("OFFLINE SYNC TRACES::addCurrentSyncStepResponse:: Sync trace does not exist", id);
      return;
    }
    this.addSyncStepResponse(id, trace.currentStep, stepResponse);
  },

  clearTrace: function clearTrace(id) {
    if (!this.syncTraces[id]) return;
    delete this.syncTraces[id].steps;
  },

  printTrace: function printTrace(id) {
    var trace = this.syncTraces[id];
    var traceLogKey = this.logKey + "TRACE::" + id + "::";
    if (bizagi.util.isEmpty(trace)) return;
    bizagi.util.console.log(traceLogKey + " ** TRACE START **", "");
    bizagi.util.console.log(traceLogKey + "LAST STEP:: " + trace.currentStep, "");
    (trace.steps || []).forEach(function (currentStep, stepIndex) {
      bizagi.util.console.log(traceLogKey + (stepIndex + 1) + "::" + currentStep.step + ":: params", currentStep.stepParameters);
      bizagi.util.console.log(traceLogKey + (stepIndex + 1) + "::" + currentStep.step + ":: response", currentStep.stepResponse);
    });
    bizagi.util.console.log(traceLogKey + " ** TRACE END **", "");
  },

  printAndClearTrace: function printAndClearTrace(id) {
    var self = this;
    setTimeout(function () {
      self.printTrace(id);
      self.clearTrace(id);
    }, 1000);
  } },


{
  // INSTANCE METHODS
  init: function init(params) {
    var self = this;
    self.renderDataService = params.renderDataService;
    self.database = params.database;
    self.fileService = self.renderDataService.getFileService();
    self.gridService = self.renderDataService.getGridService();
  },

  /*************************************************************************************************
     *          MAIN SYNC METHODS
     *************************************************************************************************/
  syncOfflineStartCase: function syncOfflineStartCase(params) {
    var self = this;
    var serverResponse;
    var offlineId = params.id || params.idCase;

    var loadFormParams = {
      action: "LOADSTARTFORM",
      idProcess: params.idProcess,
      downloadOfflineForm: true };


    bizagi.util.console.log("SYNCHRONIZING Start Case", params);
    self.Class.setCurrentSyncStep(offlineId, "getFormData", params);
    return self.renderDataService.getFormData(loadFormParams).
    then(function (response) {
      serverResponse = response;
      self.Class.addCurrentSyncStepResponse(offlineId, response);
      if (response.errorType) return $.Deferred().reject(response).promise();
      if (!response.form) return $.Deferred().reject("Form not found in server response").promise();
      self.Class.setCurrentSyncStep(offlineId, "separateComplexControls", { data: params.data });
      return self.database.separateComplexControls({ offlineType: "start" }, params.data);
    }).
    then(function (separatedCaseData) {
      self.Class.addCurrentSyncStepResponse(offlineId, separatedCaseData);
      params.pageCacheId = serverResponse.form.pageCacheId;
      params.sessionId = serverResponse.form.sessionId;
      params.formId = serverResponse.form.properties.id;
      params.offlineType = "start";
      return self.performSyncOfflineCase(params, separatedCaseData);
    }).
    then(function (performSyncResponse) {
      self.Class.clearTrace(offlineId);
      return performSyncResponse;
    }).
    fail(function (error) {
      self.Class.addCurrentSyncStepResponse(offlineId, error);
      self.Class.printAndClearTrace(offlineId);
      bizagi.util.console.error("START case sync failed", error);
      return self.updateSyncStatusError(error, "start", offlineId);
    });
  },

  syncOfflineTaskCase: function syncOfflineTaskCase(params) {
    var self = this;
    var data = params.data;
    var serverResponse;
    delete params["data"];
    var offlineId = params.idCase + "-" + params.idWorkitem;

    params.downloadOfflineForm = true;

    //Retrieve the form from server in order to obtain a valid idPageCache
    bizagi.util.console.log("SYNCHRONIZING Task Case", params);
    self.Class.setCurrentSyncStep(offlineId, "getFormData", params);
    return self.renderDataService.getFormData(params).
    then(function (response) {
      serverResponse = response;
      self.Class.addCurrentSyncStepResponse(offlineId, response);
      if (response.errorType) return $.Deferred().reject(response).promise();
      if (!response.form) return $.Deferred().reject("Form not found in server response").promise();
      self.Class.setCurrentSyncStep(offlineId, "separateComplexControls", { params: params, data: data });
      return self.database.separateComplexControls(params, data);
    }).
    then(function (separatedCaseData) {
      self.Class.addCurrentSyncStepResponse(offlineId, separatedCaseData);
      params.pageCacheId = serverResponse.form.pageCacheId;
      params.sessionId = serverResponse.form.sessionId;
      params.offlineType = "task";
      params.formId = serverResponse.form.properties.id;
      params.id = offlineId;
      return self.performSyncOfflineCase(params, separatedCaseData);
    }).
    then(function (performSyncResponse) {
      self.Class.clearTrace(offlineId);
      return performSyncResponse;
    }).
    fail(function (error) {
      self.Class.addCurrentSyncStepResponse(offlineId, error);
      self.Class.printAndClearTrace(offlineId);
      return self.updateSyncStatusError(error, "task", offlineId);
    });
  },

  /*************************************************************************************************
      *          INTERNAL METHODS
      *************************************************************************************************/

  performSyncOfflineCase: function performSyncOfflineCase(params, sData) {
    var self = this;
    var gridActions = [];
    var collectedFormActions = [];
    var entityMap;
    params.gridPromises = null;
    self.Class.setCurrentSyncStep(params.id, "checkpoint", params);
    return self.createCheckpoint(params).
    then(function (checkpointResponse) {
      self.Class.addCurrentSyncStepResponse(params.id, checkpointResponse);
      return self.processGridRecords(params, sData, gridActions, collectedFormActions);
    }).
    then(function (gridActionsResponse) {
      entityMap = self.parseGridAdditionsResponse(gridActions, gridActionsResponse);
      self.Class.addCurrentSyncStepResponse(params.id, { gridActionsResponse: gridActionsResponse, entityMap: entityMap });
      return self.processUploads(params, sData, entityMap, collectedFormActions);
    }).
    then(function (result) {
      self.Class.addSyncStepResponse(params.id, "uploadPromises", result);
      return self.processMultiAction(params, sData.data, entityMap, collectedFormActions);
    }).
    then(function (processMultiactionresponse) {
      self.Class.addSyncStepResponse(params.id, "multiaction", processMultiactionresponse);
      return processMultiactionresponse;
    }).
    fail(function (error) {
      self.Class.addCurrentSyncStepResponse(params.id, error);
      var lastAction = self.Class.getCurrentSyncStep(params.id);
      //Rollback for first checkpoint
      return self.manageError(params, error, lastAction, arguments);
    });
  },

  processGridRecords: function processGridRecords(params, data, gridActions, collectedActions) {
    var self = this;
    var gridValues = data.grids;

    if (gridValues) {
      params.gridPromises = gridPromises = self.gridService.uploadGridData(gridValues, params.pageCacheId);

      if (gridPromises.bizAdditions && gridPromises.bizAdditions.length > 0)
      Array.prototype.push.apply(gridActions, gridPromises.bizAdditions);

      if (gridPromises.bizDeletions && gridPromises.bizDeletions.length > 0)
      Array.prototype.push.apply(collectedActions, gridPromises.bizDeletions);
    }
    self.Class.setCurrentSyncStep(params.id, "multiaction grid", { gridVallues: gridValues, gridActions: gridActions });
    return self.renderDataService.multiaction().execute({ actions: gridActions });
  },

  parseGridAdditionsResponse: function parseGridAdditionsResponse(gridActions, gridActionsResponse) {
    var entityMap = {};

    if (gridActions !== undefined && gridActions.length > 0) {
      for (var i = 0; i < gridActions.length; i++) {
        if (gridActionsResponse[i] && gridActionsResponse[i].result.type === "success")
        entityMap[gridActions[i]["offline-entityId"]] = gridActionsResponse[i].result.IdEntity;
      }
    }
    return entityMap;
  },

  processUploads: function processUploads(params, data, entityMap, actions) {
    var self = this;
    var uploadPromises = [];
    var imageValues = data.images;
    var uploadValues = data.uploads;

    self.Class.setCurrentSyncStep(params.id, "uploadPromises", { imageValues: imageValues, uploadValues: uploadValues });
    try {
      if (uploadValues && uploadValues.length > 0)
      Array.prototype.push.apply(uploadPromises, self.fileService.uploadAndDeleteFormFiles(params, uploadValues, actions));

      if (imageValues && imageValues.length > 0)
      Array.prototype.push.apply(uploadPromises, self.fileService.uploadFormImages(params, imageValues));

      var gridPromises = params.gridPromises;
      if (gridPromises) {
        if (Object.keys(gridPromises.bizImages).length > 0)
        Array.prototype.push.apply(uploadPromises, self.fileService.uploadFormGridImages(params, gridPromises.bizImages, entityMap, actions));

        if (Object.keys(gridPromises.bizFiles).length > 0)
        Array.prototype.push.apply(uploadPromises, self.fileService.uploadFormGridFiles(params, gridPromises.bizFiles, entityMap, actions));
      }
    } catch (e) {
      return $.Deferred().reject(e).promise();
    }
    return $.when.apply($, uploadPromises);

  },

  processMultiAction: function processMultiAction(params, data, entityMap, actions) {
    var self = this;
    var offlineType = params.offlineType;
    var syncAction = self.getSyncAction(offlineType, data);
    var isNextAction = syncAction != undefined && syncAction.length > 0 && syncAction.toLowerCase().includes("next");
    var actions = self.prepareMultiActionData(params, data, entityMap, syncAction, actions);
    var syncActionTagResponse, multiactionResponse, commitResponse, wasActionPerformed;
    var lastAction = "multiaction";
    self.Class.setCurrentSyncStep(params.id, lastAction, actions);

    return self.renderDataService.multiaction().execute({ actions: actions }).
    then(function (response) {
      self.Class.addSyncStepResponse(params.id, "multiaction", response);

      multiactionResponse = response;
      wasActionPerformed = self.wasMultiActionPerformed(response);
      syncActionTagResponse = response[response.length - 1].result;

      if (!wasActionPerformed) return $.Deferred().reject(response).promise();

      lastAction = "commit-multiaction";
      self.Class.setCurrentSyncStep(params.id, "commit-multiaction");
      return self.performCommit(params, isNextAction);
    }).
    then(function (response) {
      commitResponse = response;
      self.Class.addSyncStepResponse(params.id, "commit-multiaction", response);
      return self.deleteOfflineCase(params, wasActionPerformed, isNextAction);
    }).
    then(function () {
      if (!wasActionPerformed) return $.Deferred().reject(multiactionResponse).promise();

      if (commitResponse != undefined && commitResponse.DataChangesetVersion != undefined) {
        syncActionTagResponse.DataChangesetVersion = commitResponse.DataChangesetVersion;
      }
      syncActionTagResponse.containsNext = $.grep(actions, function (o) {return o.h_action.toLowerCase().includes("next");}).length > 0;
      return syncActionTagResponse;
    }).
    fail(function (error) {
      self.Class.addCurrentSyncStepResponse(params.id, error);
      var cleanTaskDataActions = lastAction == "multiaction" && !params.isRetry;
      return self.performRollback(params, error, cleanTaskDataActions);
    });
  },

  prepareMultiActionData: function prepareMultiActionData(params, data, entityMap, syncAction, actions) {
    var self = this;
    var multiActionData = self.convertStartFormDataToStandardFormat(data);
    var submitDataDone = false;
    self.replaceGridChangesWithAddedRows(multiActionData, entityMap);
    var buttonActions = self.addButtonActions(params, multiActionData);
    if (buttonActions !== null && buttonActions.length > 0) {
      Array.prototype.push.apply(actions, buttonActions);
      submitDataDone = true;
    }
    var trySaveorTryNextAction = self.addTrySaveOrTryNextAction(params, syncAction, multiActionData, submitDataDone);
    if (!bizagi.util.isEmpty(trySaveorTryNextAction)) actions.push(trySaveorTryNextAction);

    return actions;
  },

  replaceGridChangesWithAddedRows: function replaceGridChangesWithAddedRows(data, entityMap) {
    if (bizagi.util.isEmpty(data)) return;

    Object.keys(data).forEach(function (key) {
      var matches = key.match(/\[id=(\d+)\]/);
      if (matches) {
        var oldKey = matches[1];
        var addedKey = entityMap[oldKey];
        if (entityMap[oldKey] !== undefined) {
          var oldXpath = key;
          var newXpath = key.replace(oldKey, addedKey);
          data[newXpath] = data[oldXpath];
          delete data[oldXpath];
        }
      }
    });
  },

  addButtonActions: function addButtonActions(params, data) {
    var self = this;
    var actions = [];

    if (bizagi.util.isEmpty(data)) return [];
    var button_actions = params.isRetry && data.retry_h_actions ? data.retry_h_actions : data.h_actions;
    delete data.h_actions;
    delete data.retry_h_actions;

    if (!Array.isArray(button_actions) || button_actions.length < 1) return [];

    var submitDataAction = self.renderDataService.submitData({
      action: "SUBMITDATA",
      data: data,
      idPageCache: params.pageCacheId,
      prepare: true });

    submitDataAction.tag = "submitData";
    actions.push(submitDataAction);

    button_actions.forEach(function (h_action_item) {
      if (h_action_item) {
        h_action_item.h_pageCacheId = params.pageCacheId;
        actions.push(h_action_item);
      }
    });

    return actions;
  },

  addTrySaveOrTryNextAction: function addTrySaveOrTryNextAction(params, syncAction, data, submitDoneBefore) {
    var self = this;
    if (!bizagi.util.isEmpty(syncAction))
    {
      // object base without data
      actionParams = {
        action: syncAction,
        idPageCache: params.pageCacheId,
        prepare: true,
        transitions: '' };


      // add data only when is required
      if (!submitDoneBefore) actionParams.data = data;

      var result = self.renderDataService.submitData(actionParams);
      result.tag = 'performAction';
      return result;
    }
    return null;
  },

  deleteOfflineCase: function deleteOfflineCase(params, wasActionPerformed, isNextAction) {
    var self = this;
    var offlineType = params.offlineType;

    if (offlineType === "start") {
      self.Class.setCurrentSyncStep(params.id, "deleteStartCase", { params: params });
      return self.database.deleteCase(params).
      then(function (deleteCaseResponse) {
        self.Class.addSyncStepResponse(params.id, "deleteStartCase", deleteCaseResponse);
        return deleteCaseResponse;
      });
    }
    // Regular activity form
    self.Class.setCurrentSyncStep(params.id, "removeSyncStatus", { params: false });
    return self.database.updateCaseSyncStatus({
      type: offlineType,
      id: params.id,
      syncStatus: undefined,
      syncDetails: undefined }).

    then(function (updateSyncStatusResponse) {
      self.Class.addSyncStepResponse(params.id, "removeSyncStatus", updateSyncStatusResponse);
      self.Class.setCurrentSyncStep(params.id, "updateTaskIsEdited", { params: false });
      return self.database.updateTaskIsEdited(params.id, false);
    }).
    then(function (updateIsEditedResponse) {
      self.Class.addSyncStepResponse(params.id, "updateTaskIsEdited", updateIsEditedResponse);
      var deleteTaskDataParams = isNextAction ? $.extend({ deleteAttachments: true }, params) : params;
      self.Class.setCurrentSyncStep(params.id, "deleteTaskData", { deleteTaskDataParams: deleteTaskDataParams });
      return self.database.deleteTaskData(deleteTaskDataParams);
    }).
    then(function (deleteTaskDataResponse) {
      self.Class.addSyncStepResponse(params.id, "deleteTaskData", deleteTaskDataResponse);
      if (offlineType === "task" && wasActionPerformed && isNextAction) {
        self.Class.setCurrentSyncStep(params.id, "deleteTaskForm", { params: params });
        return self.database.deleteTaskForm(params);
      }
      self.Class.setCurrentSyncStep(params.id, "deleteTaskForm", "--skipped--");
    }).
    then(function (deleteTaskFormResponse) {
      self.Class.addSyncStepResponse(params.id, "deleteTaskForm", deleteTaskFormResponse);
      if (offlineType === "task" && wasActionPerformed && isNextAction) {
        self.Class.setCurrentSyncStep(params.id, "deleteTaskIfNext", { params: params });
        return self.database.deleteTask(params);
      }
      self.Class.setCurrentSyncStep(params.id, "deleteTaskIfNext", "--skipped--");
      return deleteTaskFormResponse;
    }).
    then(function (deleteTaskIfNextResponse) {
      self.Class.addSyncStepResponse(params.id, "deleteTaskIfNext", deleteTaskIfNextResponse);
      return deleteTaskIfNextResponse;
    }).
    fail(function (error) {
      var deleteError = {
        type: "error",
        errorType: "offlineSyncError",
        message: "Local case could not be deleted",
        details: error };

      self.Class.addCurrentSyncStepResponse(params.id, deleteError);
      return $.Deferred().reject(deleteError).promise();
    });
  },

  /*************************************************************************************************
     *          UTIL METHODS
     *************************************************************************************************/
  convertStartFormDataToStandardFormat: function convertStartFormDataToStandardFormat(data) {
    var result = {};
    Object.keys(data).forEach(function (key) {
      result[key] = data[key];
      if (data[key].value !== undefined) {
        result[key] = data[key].value;
      }
    });
    return result;
  },

  getSyncAction: function getSyncAction(offlineType, data) {
    if (offlineType === "start") return "createnewcase";else
    if (data.h_action === "save") return "trysave";else
    if (data.h_action === "next") return "trynext";
    return data.h_action;
  },

  wasMultiActionPerformed: function wasMultiActionPerformed(res) {
    return res.length > 0 && res[res.length - 1].result && res[res.length - 1].result.type === 'success';
  },

  createCheckpoint: function createCheckpoint(params) {
    var self = this;
    return self.renderDataService.sendCheckpoint({
      idRender: params.formId,
      xpath: "",
      xpathContext: "",
      idPageCache: params.pageCacheId });

  },

  performCommit: function performCommit(params, isNextAction) {
    var self = this;
    if (isNextAction) return $.Deferred().resolve(null).promise();

    return self.renderDataService.commitGridAction({
      idRender: params.formId,
      xpath: "",
      xpathContext: "",
      idPageCache: params.pageCacheId });

  },

  manageError: function manageError(params, error, lastAction, args) {
    var self = this;
    if (lastAction != "checkpoint") {
      if (Array.isArray(error)) {
        error = error.map(function (err) {
          return self.renderDataService.parseServiceError(err);
        });
      } else
      {
        error = self.renderDataService.parseServiceError(args);
      }
      return self.performRollback(params, error);
    }
    return self.updateSyncStatusError(args, params.offlineType, params.id);
  },

  performRollback: function performRollback(params, error, cleanTaskDataActions) {
    var self = this;
    self.Class.setCurrentSyncStep(params.id, "rollback", { params: params, error: error, cleanTaskDataActions: cleanTaskDataActions });
    return self.renderDataService.rollbackGridAction({
      idRender: params.formId,
      xpath: "",
      xpathContext: "",
      idPageCache: params.pageCacheId }).

    always(function (rollbackResponse) {
      self.Class.addCurrentSyncStepResponse(params.id, rollbackResponse);
      if (!cleanTaskDataActions) {
        self.Class.setCurrentSyncStep(params.id, "cleanTaskDataActions", "--skipped--");
        return;
      }
      self.Class.setCurrentSyncStep(params.id, "cleanTaskDataActions", { id: params.id });
      return self.database.cleanTaskDataActions(params.id);
    }).
    then(function (cleanTaskDataActionsResponse) {
      self.Class.addSyncStepResponse(params.id, "cleanTaskDataActions", cleanTaskDataActionsResponse);
      if (params.offlineType == "start") {
        self.Class.setCurrentSyncStep(params.id, "openOfflineTask", "--skipped--");
        return;
      }
      self.Class.setCurrentSyncStep(params.id, "openOfflineTask", { id: params.id });
      return self.database.openOfflineTask(params.id);
    }).
    then(function (openOfflineTaskResponse) {
      self.Class.addSyncStepResponse(params.id, "openOfflineTask", openOfflineTaskResponse);
      return $.Deferred().reject(error).promise();
    });
  },

  updateSyncStatusError: function updateSyncStatusError(args, offlineType, identifier) {
    var self = this;
    var error = self.renderDataService.parseServiceError(args) || {};
    var syncErrorDetails = self.buildSyncErrorObject(error);
    return self.database.updateCaseSyncStatus({
      type: offlineType,
      id: identifier,
      syncStatus: "failed",
      syncDetails: syncErrorDetails }).

    then(function () {
      return $.Deferred().reject(error).promise();
    });
  },

  buildSyncErrorObject: function buildSyncErrorObject(error) {
    var syncErrorType = "technical";
    var message = [];
    var performActionObject = error;

    if (Array.isArray(error)) {
      error.forEach(function (singleError) {
        if (singleError.result == undefined) return;
        var responseType = (singleError.result.type || "").toLowerCase();
        if (responseType.includes("message")) {
          syncErrorType = "business";
          if (singleError.result.message) {
            message.push(singleError.result.message);
          }
          if (Array.isArray(singleError.result.messages)) {
            message = message.concat(singleError.result.messages);
          }
        }
      });
    } else
    if (error != undefined && error.result != undefined) {
      var responseType = (error.result.type || "").toLowerCase();
      if (responseType.includes("message")) {
        syncErrorType = "business";
        if (error.result.message) {
          message.push(error.result.message);
        }
        if (Array.isArray(error.result.messages)) {
          message = message.concat(error.result.messages);
        }
      }
    } else
    if (error.message) {
      message.push(error.message);
    }
    return {
      type: syncErrorType,
      message: message.length == 1 ? message[0] : message,
      actionResponse: performActionObject,
      timestamp: Date.now() };

  } });