/*
*   Name: BizAgi Diagnostisc Dabase Plugin
*   Author: Mobile Team
*   Comments:
*   -   This script creates a plugin to handle Mobile diagnostics db functionalities
*   -   Pre-requisite pouchDB
*/

bizagi.workportal.diagnostics = bizagi.workportal.diagnostics || {};
$.Class.extend("bizagi.workportal.diagnostics.db", {}, {
  /**
                                                          * Constructor
                                                          */
  init: function init() {
    this.globalId = typeof BIZAGI_USER != "undefined" ?
    ("" + BIZAGI_USER + "-" + BIZAGI_PROXY_PREFIX + "-" + BIZAGI_DOMAIN).toLowerCase() :
    "admon" + "-" + "http://localhost/bizagiR100x" + "-" + "domain";
    this.createDatabases();
  },

  /**
      * Create all the tables required
      */
  createDatabases: function createDatabases() {
    var globalIdHash = this.globalId.hashCode();
    this.dbErrors = new PouchDB("Diag_Errors_" + globalIdHash, { auto_compaction: true });
  },

  /**
      * Destroy all databases
      * Returns a promise that is resolved when all databases were successfully destroyed
      */
  deleteDatabases: function deleteDatabases() {
    var deferred = $.Deferred();
    var databases = [
    this.dbErrors];


    var deletedDatabasesCount = 0;
    var destroyCallback = function destroyCallback(err, info) {
      if (err) {
        deferred.reject(err);
      } else
      {
        deletedDatabasesCount++;
        if (deletedDatabasesCount == databases.length) {
          deferred.resolve();
        }
      }
    };

    databases.forEach(function (currentDatabase) {
      currentDatabase.destroy(destroyCallback);
    });
    return deferred.promise();
  },

  /**
      * Destroy an recreate empty tables
      */
  restartDatabases: function restartDatabases() {
    var self;
    return self.deleteDatabases().
    always(function () {
      self.createDatabases();
    });
  } });