//$.Class.extend
$.Class("bizagi.workportal.services.db", {}, {
  /* 
                                               *   Constructor
                                               */
  init: function init() {
    var self = this;

    var queryString = bizagi.readQueryString();
    self.disabledb = queryString && queryString["disabledb"] ? eval(queryString["disabledb"]) : false;

    self.globalId = typeof BIZAGI_USER != "undefined" ?
    ("" + BIZAGI_USER + "-" + BIZAGI_PROXY_PREFIX + "-" + BIZAGI_DOMAIN).toLowerCase() :
    "admon" + "-" + "http://localhost/bizagiR100x" + "-" + "domain";

    if (!self.disabledb) {
      self.createDatabases();
    }
  },

  /*
     * Delete all Documents (databases)
     * Example for callback : function (err, info) { }
     * 
     */
  _destroy: function _destroy(callback) {
    this.deleteDatabases(callback);
  },

  createDatabases: function createDatabases() {
    var self = this;
    var globalIdHash = self.globalId.hashCode();

    self.dbRecentProcesses = new PouchDB("RecentProcesses" + globalIdHash, { auto_compaction: true }); // Recent process
    self.dbProcessesHierarchy = new PouchDB("ProcessesHierarchy" + globalIdHash, { auto_compaction: true }); // Process hierarchy to new cases creation
    self.dbStartForms = new PouchDB("StartForms" + globalIdHash, { auto_compaction: true }); // To save all forms for server
    self.dbStartData = new PouchDB("StartData" + globalIdHash, { auto_compaction: true }); // Save the data from forms    

    //Offline forms in whole process
    self.dbActivities = new PouchDB("Activities" + globalIdHash, { auto_compaction: true }); // Save the offline tasks that can be worked with offline forms
    self.dbActivityForms = new PouchDB("ActivityForms" + globalIdHash, { auto_compaction: true });
    self.dbActivityData = new PouchDB("ActivityData" + globalIdHash, { auto_compaction: true }); // Cases in outbox that will be advanced
    self.dbProcessColumns = new PouchDB("ProcessColumns" + globalIdHash, { auto_compaction: true }); // Custom Columns (and metadata) for each Process
  },

  deleteDatabases: function deleteDatabases(callback) {
    var def = new $.Deferred();
    var self = this;

    var databases = [
    self.dbRecentProcesses,
    self.dbProcessesHierarchy,
    self.dbStartForms,
    self.dbStartData,
    self.dbActivities,
    self.dbActivityForms,
    self.dbActivityData,
    self.dbProcessColumns];


    var databasesDeleted = 0;
    if (typeof callback === "undefined") {
      callback = function callback(err, info) {
        if (err) {
          def.reject(err);
        } else
        {
          databasesDeleted++;
          if (databasesDeleted == databases.length) {
            def.resolve("All db deleted successfully");
          }
        }
      };
    }

    databases.forEach(function (database) {
      database.destroy(callback);
    });

    return def.promise();
  },

  restartDatabases: function restartDatabases() {
    var self = this;
    var def = new $.Deferred();
    $.when(self.deleteDatabases()).
    done(function () {
      self.createDatabases();
      def.resolve();
    }).
    fail(function () {
      self.createDatabases();
      def.resolve();
    });
    return def.promise();
  },

  getHierarchyObjects: function getHierarchyObjects(obj, key, val) {
    var self = this;
    var objects = [];
    for (var i in obj) {
      if (!obj.hasOwnProperty(i)) continue;
      if (_typeof(obj[i]) == "object") {
        objects = objects.concat(self.getHierarchyObjects(obj[i], key, val));
      } else if (i == key && obj[i] == val || i == key && val == '') {
        objects.push(obj);
      } else if (obj[i] == val && key == '') {
        // only add if the object is not already in the array
        if (objects.lastIndexOf(obj) == -1) {
          objects.push(obj);
        }
      }
    }
    return objects;
  },

  /*
     *   This method return all the open cases for the user authenticated from de DB
     *   Returns a promise
     */
  getAllOpenCases: function getAllOpenCases() {
    var self = this;
    var def = new $.Deferred();

    function map(doc) {
      if (doc.closed === false && doc.createdBy.userName === "admon") {
        emit(doc, null);
      }
    }

    self.dbStartData.query({ map: map }, { reduce: false }, function (err, response) {
      if (!err) {
        def.resolve(response);
      }
    });

    return def.promise();
  } });