  /**
      * Save all the work items as a single object
      * @param {} params 
      * @returns {} promise
      */
  saveOfflineTasks: function saveOfflineTasks(tasksMap) {
    var self = this;
    var saveQueue = new bizagiQueue();
    for (i in tasksMap) {
      var currentTask = tasksMap[i];
      var idWfClass = currentTask[10],
      id = currentTask[0] + '-' + currentTask[1];

      var offlineCase = {
        _id: id,
        endDate: currentTask.endDate,
        group: currentTask.group,
        icon: currentTask.icon,
        state: currentTask.state,
        taskState: currentTask.taskState,
        shortEndDate: currentTask.shortEndDate,
        atRiskTime: currentTask[11],
        type: currentTask.type,
        idWfClass: idWfClass.toString(),
        idWorkFlow: idWfClass.toString(),
        idCase: currentTask[0],
        isFavorite: currentTask[9],
        isEdited: false,
        createdBy: {
          Name: typeof currentTask.creatorFullName !== "undefined" ? currentTask.creatorFullName : typeof BIZAGI_USER !== "undefined" ? BIZAGI_USER : "admon",
          userName: typeof currentTask.creatorFullName !== "undefined" ? currentTask.creatorFullName : typeof BIZAGI_USER !== "undefined" ? BIZAGI_USER : "admon" },

        creationDate: currentTask[12],
        process: currentTask[4],
        categoryName: tasksMap.process || tasksMap.categoryName || "Offline",
        displayName: tasksMap.displayName || "Offline",
        radNumber: currentTask.systemColumns && currentTask.systemColumns.radnumber ? currentTask.systemColumns.radnumber : currentTask[0],
        wfClassDisplayName: "",
        // closed: false,
        isOfflineForm: true,
        formsRenderVersion: 2,
        isOpen: true,
        workItems: [
        {
          idWorkItem: currentTask[1],
          idTask: currentTask[2],
          idCase: currentTask[0],
          colorState: currentTask.taskstate,
          State: currentTask.taskstate,
          TaskName: currentTask[5] }],


        systemColumns: currentTask.systemColumns };

      saveQueue.add(self.saveOrUpdateTask(id, offlineCase));
    }
    return saveQueue.execute();
  },

  saveStartCase: function saveStartCase(caseId, offlineCase) {
    return bizagi.util.db.saveDocument(this.dbStartData, offlineCase);
  },

  saveTask: function saveTask(offlineId, offlineCase) {
    return bizagi.util.db.saveDocument(this.dbActivities, offlineCase);
  },

  saveOrUpdateTask: function saveOrUpdateTask(internalTaskId, offlineCase) {
    var def = new $.Deferred();
    var self = this;
    $.when(bizagi.util.db.getDocument(self.dbActivities, internalTaskId)).
    then(function (currentDocument) {
      $.extend(true, currentDocument, offlineCase);
      return bizagi.util.db.saveDocument(self.dbActivities, currentDocument);
    }).
    then(function () {
      def.resolve();
    }).
    fail(function (err) {
      $.when(bizagi.util.db.saveDocument(self.dbActivities, offlineCase)).
      then(function () {
        def.resolve();
      }).
      fail(function (saveError) {
        def.reject(saveError);
      });
    });

    return def.promise();
  },

  cleanTaskDataActions: function cleanTaskDataActions(internalTaskId) {
    var def = new $.Deferred();
    var self = this;
    $.when(bizagi.util.db.getDocument(self.dbActivityData, internalTaskId)).
    then(function (currentDocument) {
      if (currentDocument != undefined && currentDocument.data != undefined && currentDocument.data.h_actions != undefined) {
        currentDocument.data.retry_h_actions = currentDocument.data.h_actions;
        currentDocument.data.h_actions = [];
      }
      return bizagi.util.db.saveDocument(self.dbActivityData, currentDocument);
    }).
    always(function () {
      def.resolve();
    });

    return def.promise();
  },

