/*
*   Name: Bizagi Database Plugin
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This script creates a plugin to handler all db functionalities
*   -   Pre-requisite pouchDB
*/
$.Class.extend("bizagi.workportal.services.pullWorker", {}, {
  init: function init(params) {
    var self = this;
    var queryString = bizagi.readQueryString();

    self.services = params;
    self.database = params.database;

    var querydisable = queryString && queryString["disabledb"] ? eval(queryString["disabledb"]) : false;
    self.disabledb = querydisable;

    self.globalId = typeof BIZAGI_USER != "undefined" ?
    ("" + BIZAGI_USER + "-" + BIZAGI_PROXY_PREFIX + "-" + BIZAGI_DOMAIN).toLowerCase() :
    "admon" + "-" + "http://localhost/bizagiR100x" + "-" + "domain";
  },

  pull: function pull() {
    var self = this;
    if (self.disabledb) {
      return false;
    }


    var getTasksBeforePushPromise = self.services.callServiceWithNetworkRetries(self.services, self.services.getOfflineTasks, [], 5, 2000);
    return $.when(getTasksBeforePushPromise).
    then(function () {
      return self.syncOfflineCases();
    }, function (getOfflineTasksError) {
      bizagi.util.console.log("Error in getOfflineTasks before to push cases", getOfflineTasksError);
      //Resolve the promise with sucess=true in order to don't show error message
      return $.Deferred().resolve(true).promise();
    });


  },

  /**
      * This function send to the server the offline cases created
      * @param {} argument 
      * @returns {} 
      */
  syncOfflineCases: function syncOfflineCases(argument) {
    var self = this;
    if (typeof self.database !== "undefined") {
      self.synchronizing = self.synchronizing || false;

      if (!self.synchronizing) {
        self.synchronizing = true;
        self.pushInProgressPromise = new $.Deferred();
        var syncCasesQueue = new bizagiQueue();
        var syncStarts = self.syncOfflineStartCases(syncCasesQueue);
        var syncTasks = self.syncOfflineTaskCases(syncCasesQueue);

        $.when(syncStarts, syncTasks).
        then(function (startToSync, tasksToSync) {

          if (tasksToSync && tasksToSync.count > 0) {
            var syncInProgressParams = {
              type: "push-tasks-started",
              tasks: tasksToSync.tasks || {} };

            $(document).trigger("offline-notify-form-sync-event", syncInProgressParams);
          }

          // By telemetry [Offline] [Sync] [Start Case] [Success]
          if (startToSync && startToSync.count > 0) {
            console.log('[Offline] [Sync] [Start Case] [Success]', startToSync.count);

            if (bizagi.util.isNativePluginSupported()) {
              var startCaseSuccessEvent = {
                type: 'success',
                description: '[Offline] [Sync] [Start Case] [Success]',
                cases_number: startToSync.count };


              if (typeof bizagiapp.sendStartFormSyncEvent !== "undefined") {
                bizagiapp.sendStartFormSyncEvent(startCaseSuccessEvent);
              }
            }
          }

          // By telemetry [Offline] [Sync] [Task Case] [Success]
          if (tasksToSync && tasksToSync.count > 0) {
            console.log('[Offline] [Sync] [Task Case] [Success]', tasksToSync.count);

            if (bizagi.util.isNativePluginSupported()) {
              var taskCaseSuccessEvent = {
                type: 'success',
                description: '[Offline] [Sync] [Task Case] [Success]',
                cases_number: tasksToSync.count };


              if (typeof bizagiapp.sendTaskFormSyncEvent !== "undefined") {
                bizagiapp.sendTaskFormSyncEvent(taskCaseSuccessEvent);
              }
            }
          }

          return syncCasesQueue.execute();
        }).
        then(function () {
          setTimeout(function () {
            self.synchronizing = false;
            self.pushInProgressPromise.resolve(true);
          }, 1000);
        }).
        fail(function (error) {
          setTimeout(function () {

            // By telemetry [Offline] [Sync] [Task Case] [Start Case] [Error]
            if (error) {
              console.log('[Offline] [Sync] [Task Case] [Start Form] [Error]');

              if (bizagi.util.isNativePluginSupported()) {
                var errorEvent = {
                  type: 'error',
                  description: '[Offline] [Sync] [Task Case] [Start Case] [Error]',
                  cases_number: 0 };


                if (typeof bizagiapp.sendStartFormSyncEvent !== "undefined") {
                  bizagiapp.sendStartFormSyncEvent(errorEvent);
                }

                if (typeof bizagiapp.sendTaskFormSyncEvent !== "undefined") {
                  bizagiapp.sendTaskFormSyncEvent(errorEvent);
                }
              }
            }

            console.log("Error pushing cases to server with errors", error);
            self.synchronizing = false;
            $(document).trigger("offline-notify-form-sync-event", { type: "push-tasks-error", message: self.parseSyncError(error) });
            self.pushInProgressPromise.reject(error);
          }, 1000);
        });
      }
    }

    return self.pushInProgressPromise.promise();
  },

  parseSyncError: function parseSyncError(err) {
    if (err.message) return err.message;
    if (err.length && err.length > 0 && err[err.length - 1].result && err[err.length - 1].result.messages.length && err[err.length - 1].result.messages.length > 0) return err[err.length - 1].result.messages[0];
    return "Unknown error";
  },

  syncSingleOfflineTaskCase: function syncSingleOfflineTaskCase(params) {
    var self = this;
    var def = new $.Deferred();
    params.isSingleSync = true;
    $.when(self.database.getTaskData(params)).
    then(function (taskData) {
      var syncCaseParams = {
        idCase: params.idCase,
        idWorkitem: params.idWorkitem,
        data: taskData.data || {},
        type: "task",
        id: params.idCase + "-" + params.idWorkitem,
        syncStatus: "started",
        isSingleSync: params.isSingleSync,
        isRetry: params.isRetry === true };

      return self.generateSyncCasePromise(syncCaseParams);
    }).
    then(function (pushResult) {
      var downloadFormAgainPromise = new $.Deferred();
      var shouldDownloadFormAgain = false;

      if (pushResult && !pushResult.containsNext) {
        if (pushResult && pushResult.DataChangesetVersion != undefined) {
          $.when(self.database.getTaskForm(params)).
          then(function (localTaskForm) {
            localTaskForm = localTaskForm.form;
            if (localTaskForm.DataChangesetVersion != undefined && pushResult.DataChangesetVersion > localTaskForm.DataChangesetVersion) {
              shouldDownloadFormAgain = true;
              pushResult.shouldRefresh = true;
            }
          }).
          fail(function (error) {
            shouldDownloadFormAgain = true;
          }).
          always(function () {
            downloadFormAgainPromise.resolve(shouldDownloadFormAgain, pushResult);
          });
        } else
        {
          shouldDownloadFormAgain = true;
          downloadFormAgainPromise.resolve(shouldDownloadFormAgain, pushResult);
        }
      } else
      {
        downloadFormAgainPromise.resolve(shouldDownloadFormAgain, pushResult);
      }
      return downloadFormAgainPromise.promise();
    }).
    then(function (shouldDownloadFormAgain, pushResult) {
      if (shouldDownloadFormAgain) {
        return $.when(self.services.downloadOfflineTaskForm(params)).
        then(function (downloadResult) {
          return pushResult;
        });
      } else
      {
        return pushResult;
      }
    }).
    then(function (pushResult) {

      // By telemetry [Online] [Sync] [Task Case] [Success] [Feature flag Force Offline on true]
      console.log('[Online] [Sync] [Task Case] [Success] [Feature flag Force Offline on true]');

      if (bizagi.util.isNativePluginSupported()) {
        var taskCaseSuccessEvent = {
          type: 'success',
          description: '[Online] [Sync] [Task Case] [Success] [Feature flag Force Offline on true]',
          cases_number: 0 };


        if (typeof bizagiapp.sendTaskFormSyncEvent !== "undefined") {
          bizagiapp.sendTaskFormSyncEvent(taskCaseSuccessEvent);
        }
      }

      def.resolve(pushResult);
    }).
    fail(function (syncTaskError) {

      // By telemetry [Online] [Sync] [Task Case] [Error] [Feature flag Force Offline on true]
      console.log('[Online] [Sync] [Task Case] [Error] [Feature flag Force Offline on true]');

      if (bizagi.util.isNativePluginSupported()) {
        var errorEvent = {
          type: 'error',
          description: '[Online] [Sync] [Task Case] [Error] [Feature flag Force Offline on true]',
          cases_number: 0 };


        if (typeof bizagiapp.sendTaskFormSyncEvent !== "undefined") {
          bizagiapp.sendTaskFormSyncEvent(errorEvent);
        }
      }

      if (params.isRetry !== true) {
        def.reject(syncTaskError);
      } else
      {
        syncTaskError = self.services.renderDataService.parseServiceError(arguments);
        var internalSyncError = self.services.syncService.buildSyncErrorObject(syncTaskError);
        var errorResponse = {
          _internalSyncError: internalSyncError,
          message: internalSyncError != undefined && internalSyncError.message ? internalSyncError.message : undefined };

        $.extend(errorResponse, syncTaskError);
        def.reject(errorResponse);
      }
    });
    return def.promise();
  },

  syncSingleOfflineStartCase: function syncSingleOfflineStartCase(params) {
    var self = this;
    var def = new $.Deferred();
    $.when(self.database.getCase(params.idCase)).
    then(function (newCase) {
      var syncCaseParams = {
        idCase: newCase.idCase,
        data: newCase.data || {},
        idProcess: newCase.idWorkFlow,
        type: "start",
        id: newCase.idCase,
        syncStatus: "started",
        isRetry: params.isRetry === true };

      return self.generateSyncCasePromise(syncCaseParams);
    }).
    then(function (syncResponse) {

      // By telemetry [Online] [Sync] [Start Case] [Success] [Feature flag Force Offline on true]
      console.log('[Online] [Sync] [Start Case] [Success] [Feature flag Force Offline on true]');

      if (bizagi.util.isNativePluginSupported()) {
        var startCaseSuccessEvent = {
          type: 'success',
          description: '[Online] [Sync] [Start Case] [Success] [Feature flag Force Offline on true]',
          cases_number: 1 };


        if (typeof bizagiapp.sendStartFormSyncEvent !== "undefined") {
          bizagiapp.sendStartFormSyncEvent(startCaseSuccessEvent);
        }
      }

      def.resolve(syncResponse);
    }).
    fail(function (syncCaseError) {

      // By telemetry [Online] [Sync] [Start Case] [Error] [Feature flag Force Offline on true]
      console.log('[Online] [Sync] [Start Case] [Error][Feature flag Force Offline on true]');

      if (bizagi.util.isNativePluginSupported()) {
        var errorEvent = {
          type: 'error',
          description: '[Online] [Sync] [Start Case] [Error] [Feature flag Force Offline on true]',
          cases_number: 0 };


        if (typeof bizagiapp.sendStartFormSyncEvent !== "undefined") {
          bizagiapp.sendStartFormSyncEvent(errorEvent);
        }
      }

      syncCaseError = self.services.renderDataService.parseServiceError(arguments);
      var internalSyncError = self.services.syncService.buildSyncErrorObject(syncCaseError);
      var errorResponse = {
        _internalSyncError: internalSyncError,
        message: internalSyncError != undefined && internalSyncError.message ? internalSyncError.message : undefined };

      $.extend(errorResponse, syncCaseError);
      def.reject(errorResponse);
    });
    return def.promise();
  },

  syncOfflineStartCases: function syncOfflineStartCases(synchCasesQueue) {
    var self = this;
    var def = new $.Deferred();
    $.when(self.database.getOutboxCases()).
    done(function (response) {
      for (var i = 0; i < response.rows.length; i++) {
        var newCase = response.rows[i].value;

        if (newCase.syncStatus) {
          continue;
        }

        var syncCaseParams = {
          idCase: newCase.idCase,
          data: newCase.data || {},
          idProcess: newCase.idWorkFlow,
          type: "start",
          id: newCase.idCase,
          syncStatus: "started" };


        synchCasesQueue.add(self.generateSyncCasePromise(syncCaseParams));
      }
      def.resolve({ "count": response.rows.length });
    }).
    fail(function (err) {
      def.reject(err);
    });
    return def.promise();
  },


  syncOfflineTaskCases: function syncOfflineTaskCases(synchCasesQueue) {
    var def = new $.Deferred();
    var self = this;
    var syncStatusCriteria = "none";
    self.database.getOutboxTaskCasesFilteredBySyncStatus(syncStatusCriteria, true).done(
    function (responses) {
      var tasks = {};
      for (var i = 0; i < responses.length; i++) {
        var caseData = responses[i].doc;
        var itemId = responses[i].id;
        var id = itemId.split('-');
        var syncCaseParams = {
          idCase: id[0],
          idWorkitem: id[1],
          data: caseData.data || {},
          type: "task",
          id: itemId,
          syncStatus: "started" };

        tasks[itemId] = true;
        synchCasesQueue.add(self.generateSyncCasePromise(syncCaseParams));
      }
      def.resolve({ "tasks": tasks, "count": responses.length });
    }).

    fail(function (err) {
      def.reject(err);
    });
    return def.promise();
  },


  generateSyncCasePromise: function generateSyncCasePromise(syncCaseParams) {
    var self = this;
    if (syncCaseParams.type === "start" || syncCaseParams.data && (syncCaseParams.data.h_action ||
    syncCaseParams.data.h_actions || syncCaseParams.data.retry_h_actions)) {

      return self.database.updateCaseSyncStatus(syncCaseParams).
      then(function () {
        var syncPromise;
        if (syncCaseParams.type == "start")
        syncPromise = self.services.syncOfflineStartCase(syncCaseParams);else

        syncPromise = self.services.syncOfflineTaskCase(syncCaseParams);
        return syncPromise;
      });
    } else
    {
      //The h_action was not specified.
      bizagi.util.console.log("This case will not be synchronized. No actions found", syncCaseParams);
      return $.Deferred().resolve("This case will not be synchronized. No actions found").promise();
    }
  } });