/*
*   Name: Bizagi Rendering Services
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This class will provide a facade to access to workportal REST services
*/
bizagi.file = bizagi.file || {};
bizagi.file.services = bizagi.file.services || {};
if (!bizagi.file.services.service) {
  $.Class.extend("bizagi.file.services.service", {
    // STATIC METHODS
    loaders: {},

    isLoading: function isLoading(key) {
      return this.loaders[key] || false;
    },

    setLoading: function setLoading(idCase, idWorkitem, xpathContext, xpath) {
      var key = this.internalBuildOfflineFileLoadingKey(idCase, idWorkitem, xpathContext, xpath);
      this.loaders[key] = true;
    },

    clearLoading: function clearLoading(idCase, idWorkitem, xpathContext, xpath) {
      var key = this.internalBuildOfflineFileLoadingKey(idCase, idWorkitem, xpathContext, xpath);
      this.loaders[key] = false;
    },

    internalBuildOfflineFileLoadingKey: function internalBuildOfflineFileLoadingKey(idCase, idWorkitem, xpathContext, xpath) {
      var key = "case:" + idCase;
      if (!bizagi.util.isEmpty(idWorkitem)) key += "-" + idWorkitem;
      key += "::";

      key += "xpath:";
      if (!bizagi.util.isEmpty(xpathContext)) key += xpathContext + ".";
      key += xpath;

      return key;
    } },

  {
    EMPTY_IMAGE: "R0lGODlhAQABAIAAAP7//wAAACH5BAAAAAAALAAAAAABAAEAAAICRAEAOw==",

    // INSTANCE METHODS
    init: function init(params) {
      var self = this;
      self.renderDataService = params.renderDataService;
      self.database = params.database;
    },

    isLoading: function isLoading(control)
    {
      return this.Class.isLoading(this.buildOfflineFileLoadingKey(control));
    },

    buildOfflineFileLoadingKey: function buildOfflineFileLoadingKey(control) {
      var form = control.getFormContainer();
      var idCase = form.getParams().idCase;
      var idWorkitem = form.getParams().idWorkitem;

      return this.Class.internalBuildOfflineFileLoadingKey(idCase, idWorkitem, control.properties.xpathContext, control.properties.xpath);
    },

    /*************************************************************************************************
        *          DOWNLOAD IMAGE METHODS
        *************************************************************************************************/
    downloadFormImages: function downloadFormImages(form, params) {
      var self = this;
      var imageControls = self.database.filterXpathsByTypeMap(form, { image: true, columnImage: true, signature: true });
      var promises = [];

      // Create promises to add in multi-action
      imageControls.forEach(function (imageControl) {
        var id = self.buildOfflineImageId(imageControl.value, imageControl.xpathContext, imageControl.xpath);
        if (params.localFiles[id]) return;

        // In this case we won't wait for promise, because we wany async loading of this
        promises.push(self.downloadImage(params, form.form, imageControl));
      });

      return promises;
    },

    downloadImage: function downloadImage(params, form, imageControl) {
      var self = this;
      var isVersion2OrSuperior = form.properties.compatibilityVersion >= 2;

      if (isVersion2OrSuperior && bizagi.util.isEmpty(imageControl.value)) return $.Deferred().resolve(null).promise();

      var getPropertyDataParams = {
        xpath: imageControl.xpath,
        idRender: imageControl.renderId,
        xpathContext: imageControl.xpathContext || "",
        idPageCache: form.pageCacheId,
        sessionId: form.sessionId,
        property: isVersion2OrSuperior ? "fileBinaryContent" : "fileFullContent" };


      if (isVersion2OrSuperior) {
        return self.downloadImageVersion2(params, getPropertyDataParams, form, imageControl);
      }

      return self.downloadImageVersion1(params, getPropertyDataParams, form, imageControl);
    },

    downloadImageVersion2: function downloadImageVersion2(params, getUploadFileUrlParams, form, imageControl) {
      var self = this;
      self.Class.setLoading(params.idCase, params.idWorkitem, imageControl.xpathContext, imageControl.xpath);
      getUploadFileUrlParams.queue = params.bulkDownload ? "offlineimages" : undefined;

      return self.renderDataService.downloadFile(getUploadFileUrlParams).
      then(function (binary_file) {

        binary_file.name = imageControl.xpath;
        params.operationType = "synchronization";
        params.id = self.buildOfflineImageId(imageControl.value, imageControl.xpathContext, imageControl.xpath);
        return self.database.saveActivityFormImage(params, binary_file, binary_file.type);
      }).
      then(function () {
        if (!params.bulkDownload) return self.ensureImageIsPersistedLocally(params);
        return;
      }).
      always(function () {
        self.Class.clearLoading(params.idCase, params.idWorkitem, imageControl.xpathContext, imageControl.xpath);
      });
    },

    ensureImageIsPersistedLocally: function ensureImageIsPersistedLocally(params, counter) {
      var self = this;
      counter = counter || 3;
      if (counter == 0) {
        console.log("downloadImageVersion2::image not found after 3 retries '" + params.id + "'", params);
        return $.Deferred().resolve().promise();
      }

      // PERFORM A IMAGE LOADING IMMEDIATELY, TO MAKE SURE THE IMAGE IS THERE [BUG QA: 42953]
      // **  ATTEMPT 3 TIMES WITH 200 ms of wait **
      var getOfflineImageParams = {
        idCase: params.idCase,
        idWorkitem: params.idWorkitem,
        id: params.id,
        offlineType: "task",
        loadImageFromForm: false };

      var invokeAgain = false;
      return self.renderDataService.getOfflineImage(getOfflineImageParams, bizagi.util.db.AttachmentType.Blob).
      then(function (blob) {
        if (blob == null) {
          //bizagi.util.console.log("downloadImageVersion2::image not found '" + params.id + "' ... retrying " + counter + "more times", params);
          invokeAgain = true;
          return bizagi.util.sleepFor(200);
        }
        return;
      }).
      then(function () {
        if (invokeAgain) return self.ensureImageIsPersistedLocally(params, counter - 1);
        return;
      });
    },

    downloadImageVersion1: function downloadImageVersion1(params, getPropertyDataParams, form, imageControl) {
      var self = this;
      self.Class.setLoading(params.idCase, params.idWorkitem, imageControl.xpathContext, imageControl.xpath);

      return self.renderDataService.multiaction().getPropertyData(getPropertyDataParams).
      then(function (base64Image) {
        if (base64Image == undefined) return;

        var saveParams = {
          id: imageControl.xpathContext ? imageControl.xpathContext + '.' + imageControl.xpath : imageControl.xpath,
          idCase: params.idCase,
          idWorkitem: params.idWorkitem,
          operationType: "synchronization" };

        return self.database.saveActivityFormImage(saveParams, base64Image);
      }).
      always(function () {
        self.Class.clearLoading(params.idCase, params.idWorkitem, imageControl.xpathContext, imageControl.xpath);
      });
    },

    /*************************************************************************************************
        *          DOWNLOAD FILE METHODS
        *************************************************************************************************/
    downloadFormFiles: function downloadFormFiles(form, params) {
      var self = this;
      var data = {};
      var promises = [];
      var uploads = self.database.filterXpathsByTypeMap(form, { upload: true, columnUpload: true });
      uploads.forEach(function (upload) {
        var result = self.downloadFilesInUploadControl(params, form, upload);
        Array.prototype.push.apply(promises, result);
        if (upload.type !== "columnUpload") //Grid handles convertion by cell
          data[upload.xpath] = self.database.convertUploadOnlineValueInOfflineValue(upload.value);
      });

      return {
        data: data,
        promises: promises };

    },

    downloadFilesInUploadControl: function downloadFilesInUploadControl(params, form, uploadControl) {
      var self = this;
      var promises = [];

      if (uploadControl.value && Array.isArray(uploadControl.value)) {
        uploadControl.value.forEach(function (singleFileValue) {
          var fileId = singleFileValue[0];
          var fileName = singleFileValue[1];
          var fullXpath = (uploadControl.xpathContext ? uploadControl.xpathContext + '.' : '') + uploadControl.xpath + '.' + fileId;
          if (params.localFiles[fullXpath]) return;

          // In this case we won't wait for promise, because we wany async loading of this
          promises.push(self.downloadSingleFile(params, form, uploadControl, fileId, fileName, fullXpath));
        });
      }
      return promises;
    },

    downloadSingleFile: function downloadSingleFile(params, form, uploadControl, fileId, fileName, fullXpath) {
      var self = this;
      return self.downloadBinaryFile(params, form, uploadControl, fileId, fileName).
      then(function (binaryFile) {
        if (binaryFile) {
          params.operationType = "synchronization";
          params.id = fullXpath;
          self.renderDataService.saveOfflineFile(params, binaryFile, binaryFile.type);
        }
      });
    },

    downloadBinaryFile: function downloadBinaryFile(params, form, uploadControl, fileId, fileName) {
      var self = this;
      var getFileUrlParams = {
        xpath: uploadControl.xpath,
        idRender: uploadControl.renderId,
        xpathContext: uploadControl.xpathContext || "",
        idPageCache: form.form.pageCacheId,
        fileId: fileId,
        sessionId: form.form.sessionId,
        queue: params.bulkDownload ? "offlinefiles" : undefined };


      return self.renderDataService.downloadFile(getFileUrlParams).
      then(function (binary_file) {
        binary_file.name = fileName;
        return binary_file;
      });
    },



    /*************************************************************************************************
        *          UPLOAD IMAGE METHODS
        *************************************************************************************************/
    uploadFormImages: function uploadFormImages(params, imageValues) {
      var self = this;
      var promises = [];
      imageValues.forEach(function (imageControl) {
        var data = imageControl.value;
        if (!data) return;

        $.extend(params, imageControl);
        promises.push(self.uploadImage(params, data));
      });

      return promises;
    },

    uploadFormGridImages: function uploadFormGridImages(params, imagesByRow, entityMap) {
      var self = this;
      var rowKeys = Object.keys(imagesByRow);
      var promises = [];

      for (var k = 0; k < rowKeys.length; k++) {
        var row = imagesByRow[rowKeys[k]];
        var imagesKeys = Object.keys(row);

        for (var l = 0; l < imagesKeys.length; l++) {

          if (imagesKeys[l] != "isNew") {
            var imageInfo = row[imagesKeys[l]];
            if (row.isNew) {
              //Use created entity id
              var newKey = entityMap[rowKeys[k]];
              if (newKey === undefined) continue;
              imageInfo.xpathContext = imageInfo.gridXpath + self.database.buildCellXpathFilter(newKey);
            } else {
              imageInfo.xpathContext = imageInfo.gridXpath + self.database.buildCellXpathFilter(rowKeys[k]);
            }

            if (!imageInfo) continue;

            //Column image case :: TM20896 :: EnabledUploadFilesInTable Requiere this XPath Alone
            if (imageInfo.columnXpath) params.xpath = imageInfo.columnXpath;

            uploadParams = $.extend({}, params, imageInfo);
            promises.push(self.uploadImage(uploadParams, imageInfo));
          }

        }
      }

      return promises;
    },

    uploadImage: function uploadImage(params, data) {
      var self = this;
      var addParams = self.buildAddParamsForUpload(params, 'savefile');
      var uploadFileName = !bizagi.util.isEmpty(data.name) ? data.name : "offlineImage_" + bizagi.util.randomNumber() + ".png";
      var uploadKey = params.xpath;
      var uploadAddUrl = self.renderDataService.getUploadAddFileUrl(false);

      var formData = new FormData();
      $.each(addParams, function (key, item) {
        formData.append(key, item);
      });
      return self.renderDataService.getOfflineImage({
        idCase: params.idCase,
        idWorkitem: params.idWorkitem,
        id: data.offlineId,
        offlineType: params.offlineType,
        loadImageFromForm: false },
      bizagi.util.db.AttachmentType.Blob).
      then(function (binaryFile) {
        if (data.isSignature && binaryFile == null)
        binaryFile = bizagi.util.b64toBlob(self.EMPTY_IMAGE, "image/png");

        if (binaryFile) {
          formData.append(uploadKey, binaryFile, uploadFileName);
          return $.ajax({ url: uploadAddUrl, type: "POST", contentType: false, processData: false, data: formData });
        }

        // Failed getOfflineImage
        bizagi.util.console.error("UploadImage:: Missing local file", params, data.offlineId);
        return $.Deferred().reject({ type: "error", errorType: "offlineUploadFileError", message: "Missing local file " + data.offlineId }).promise();
      }).
      then(function (resp) {
        if (resp.type && resp.type === "error") {
          bizagi.util.console.error("UploadImage:: Server returned error", params, resp);
          return $.Deferred().reject(resp).promise();
        }

        bizagi.util.console.log("Image uploaded", resp, uploadKey, uploadFileName);
        return resp;
      }).
      fail(function (error) {
        bizagi.util.console.error("UploadImage:: fail", error);
      });
    },

    /*************************************************************************************************
        *          UPLOAD FILE METHODS
        *************************************************************************************************/

    uploadAndDeleteFormFiles: function uploadAndDeleteFormFiles(params, uploadValues, actions) {
      var self = this;
      var promises = [];
      uploadValues.forEach(function (uploadControlValue) {
        Array.prototype.push.apply(promises, self.uploadAndDeleteFiles(params, uploadControlValue, actions));
      });

      return promises;
    },

    uploadFormGridFiles: function uploadFormGridFiles(uploadParams, filesByRow, entityMap, actions) {
      var self = this;
      var promises = [];

      var rowKeys = Object.keys(filesByRow);
      for (var k = 0; k < rowKeys.length; k++) {
        var row = filesByRow[rowKeys[k]];
        var filesKeys = Object.keys(row);
        for (var l = 0; l < filesKeys.length; l++) {
          if (filesKeys[l] != "isNew") {
            var fileInfo = row[filesKeys[l]];
            if (row.isNew) {
              //Use created entity id
              var newKey = entityMap[rowKeys[k]];
              if (newKey === undefined) continue;
              fileInfo.xpathContext = fileInfo.gridXpath + self.database.buildCellXpathFilter(newKey);
            } else {
              fileInfo.xpathContext = fileInfo.gridXpath + self.database.buildCellXpathFilter(rowKeys[k]);
            }
            Array.prototype.push.apply(promises, self.uploadAndDeleteFiles(uploadParams, fileInfo, actions));
          }

        }
      }

      return promises;
    },

    uploadAndDeleteFiles: function uploadAndDeleteFiles(params, uploadControl, deleteActions) {
      var promises = [];
      var self = this;
      var files = uploadControl.value;
      if (!files || files.length == 0) {
        return;
      }

      files.forEach(function (file) {
        var status = file.status;
        if ("new" === status) {
          var uploadParams = {
            fileId: file.id,
            fileName: file.fileName,
            fileMimeType: file.mimeType };

          $.extend(uploadParams, params);
          promises.push(self.uploadFile(uploadParams, uploadControl));

        } else if ("deleted" === status) {
          var deleteParams = {
            renderId: uploadControl.renderId,
            xpath: uploadControl.xpath,
            fileId: file.id,
            idPageCache: params.pageCacheId };

          //Column file scenario
          if (uploadControl.columnXpath) {
            deleteParams.xpath = uploadControl.columnXpath;
            deleteParams.xpathContext = uploadControl.xpathContext;
          }
          var deleteAction = self.deleteFile(deleteParams);
          deleteActions.push(deleteAction);
        }
      });

      return promises;
    },

    uploadFile: function uploadFile(params, uploadControl) {
      var self = this;
      var xpath = uploadControl.xpath;

      //Column file scenario
      if (uploadControl.columnXpath) {
        xpath = uploadControl.columnXpath;
        var xpathContext = uploadControl.xpathContext ? uploadControl.xpathContext + "." : "";
        var attachmentId = xpathContext + xpath + '.' + params.fileId;
        if (uploadControl.isNew && uploadControl.fullXpath) {
          attachmentId = uploadControl.fullXpath + "." + params.fileId;
        }
      } else
      {
        var attachmentId = xpath + '.' + params.fileId;
      }

      return self.getOfflineFile(params, attachmentId).
      then(function (blobFile) {
        if (blobFile) {
          var uploadFileParams = {
            xpath: xpath,
            renderId: uploadControl.renderId,
            pageCacheId: params.pageCacheId,
            sessionId: params.sessionId,
            fileName: params.fileName,
            xpathContext: uploadControl.xpathContext };

          return self.uploadBinaryFile(uploadFileParams, blobFile);
        }

        bizagi.util.console.error("UploadFile:: Missing local file", params, attachmentId);
        return $.Deferred().reject({
          type: "error",
          errorType: "offlineUploadFileError",
          message: "Missing local file " + attachmentId }).
        promise();
      });
    },


    uploadBinaryFile: function uploadBinaryFile(params, binaryFile) {
      var self = this;
      var addParams = self.buildAddParamsForUpload(params, 'addfile');
      var formData = new FormData();
      $.each(addParams, function (key, item) {
        formData.append(key, item);
      });
      formData.append(params.xpath, binaryFile, params.fileName);

      return self.renderDataService.processUploadFile({
        url: self.renderDataService.getUploadAddUrl(),
        formData: formData }).

      then(function (res) {
        if (res.type && res.type === "error") {
          bizagi.util.console.error("UploadBinaryFile:: Server returned error", params, res);
          return $.Deferred().reject(res).promise();
        } else
        {
          bizagi.util.console.log("File uploaded", res, params.xpath, params.fileName);
          return res;
        }
      });
    },

    getOfflineFile: function getOfflineFile(params, attachmentId) {
      var self = this;
      var offlineType = params.offlineType;
      var getFileParams = {
        id: attachmentId,
        idCase: params.idCase };


      if (offlineType && offlineType === "start") {
        getFileParams.offlineType = params.offlineType;
        return self.renderDataService.getOfflineFile(getFileParams, bizagi.util.db.AttachmentType.Blob, params.fileMimeType);
      }

      // Activity form
      getFileParams.idWorkitem = params.idWorkitem;
      return self.renderDataService.getOfflineActivityDataFile(getFileParams, bizagi.util.db.AttachmentType.Blob, params.fileMimeType);
    },

    deleteFile: function deleteFile(params) {
      var self = this;
      var fileId = params.fileId;
      var fileXpath = params.xpath + (bizagi.util.isNumeric(fileId) ? "[id=" + fileId + "]" : "[id='" + fileId + "']");
      var deleteParams = {
        xpath: fileXpath,
        idRender: params.renderId,
        xpathContext: params.xpathContext || "",
        idPageCache: params.idPageCache,
        prepare: true };

      return self.renderDataService.deleteUploadFile(deleteParams);
    },

    /*************************************************************************************************
        *          UTIL METHODS
        *************************************************************************************************/

    buildOfflineImageId: function buildOfflineImageId(value, xpathContext, xpath, isVersion2OrSuperior, isStartForm) {
      isVersion2OrSuperior = isVersion2OrSuperior || (isVersion2OrSuperior === undefined ? true : false);
      if (bizagi.util.isEmptyValue(value)) value = null;
      value = bizagi.util.isArray(value) && bizagi.util.isArray(value[0]) ? value[0][1] : value;

      var result = (xpathContext ? xpathContext + '.' : '') + xpath;
      if (isVersion2OrSuperior && !bizagi.util.isEmpty(value) && !isStartForm) {
        result += '.' + value;
      }
      return result;
    },

    getOfflineImageName: function getOfflineImageName(value, forceChange) {
      if (bizagi.util.isEmptyValue(value)) value = null;
      var result = bizagi.util.isArray(value) && bizagi.util.isArray(value[0]) ? value[0][1] : null;
      if (forceChange) {
        result = "offlineImage_" + bizagi.util.randomNumber() + ".png";
      }
      return result;
    },

    changeOfflineImageName: function changeOfflineImageName(value, imageName) {
      if (bizagi.util.isEmptyValue(value)) return;
      if (bizagi.util.isArray(value) && bizagi.util.isArray(value[0])) value[0][1] = imageName;
    },

    resolveBlobFromPath: function resolveBlobFromPath(path, fileName) {
      var blobPromise = new $.Deferred();
      var self = this;
      var isNative = bizagi.util.isCordovaSupported();
      if (isNative) {
        if (!bizagi.util.isAndroidDevice()) {
          path = bizagi.util.media.normalizeIOSFilePath(path);
        } else
        {
          // Fix android 4.4 getting images from recent folder
          path = bizagi.util.media.getImagePath(path);
        }

        var errorHandler = function errorHandler(error) {
          bizagi.util.console.error('Error converting path to blob', error);
          blobPromise.reject(error);
        };

        window.resolveLocalFileSystemURL(path, function (fileEntry) {
          fileEntry.file(function (file) {
            var reader = new FileReader();
            reader.onloadend = function () {
              var blob = new Blob([this.result], { type: file.type });
              blob.name = fileName;
              blobPromise.resolve(blob);
            };
            reader.readAsArrayBuffer(file);
          }, errorHandler);
        }, errorHandler);
      } else
      {
        //In browser is the File
        blobPromise.resolve(path);
      }
      return blobPromise;
    },

    buildAddParamsForUpload: function buildAddParamsForUpload(params, action) {
      var addParams = {};
      addParams["h_xpath"] = params.xpath;
      addParams["h_idRender"] = params.renderId;
      addParams["h_xpathContext"] = params.xpathContext || "";
      addParams["h_pageCacheId"] = params.pageCacheId;
      addParams["p_sessionId"] = params.sessionId;
      addParams["h_action"] = action;

      try {
        BIZAGI_SESSION_NAME != undefined ?
        addParams[BIZAGI_SESSION_NAME] = params.sessionId :
        addParams["JSESSIONID"] = params.sessionId;
      } catch (e) {
        addParams["JSESSIONID"] = params.sessionId;
      }
      addParams.queueID = "q_" + bizagi.util.encodeXpath(params.xpath);
      return addParams;
    } });

}