  /**
      *  This method return the categories like the services if exists in the database
      * @param {} params
      * @param {} data
      * @returns {} Returns a promise
      */
  setRenderValues: function setRenderValues(params, data, options) {
    var type = params.type;

    if (!type) type = "label";

    var typeGroupBasic = ["text", "label", "number", "money", "date", "boolean", "geolocation", "hidden", "upload"];
    var typeGroupCombo = ["combo", "cascadingCombo", "radio", "list"];

    if (typeGroupBasic.indexOf(type) !== -1) {
      params.value = data.value;
      return;
    }

    if (typeGroupCombo.indexOf(type) !== -1) {
      for (var i = 0, len = params.data.length; i < len; i++) {
        if (params.data[i].id == data.value) {
          params.value = [{ id: data.value, value: params.data[i].value }];
        }
      }

      return;
    }

    if (type === "search") {
      for (var j = 0, lengthData = params.data.length; j < lengthData; j++) {
        if (params.data[j].id == data.value.id) {
          params.value = data.value;
        }
      }
      return;
    }

    if (["image", "signature"].includes(type)) {
      params.value = [[0, data.value.name]];
      params.binaryValue = data.value;
      return;
    }

    if (type === "grid") {
      //New complex object for grids
      params["offline-files"] = data.value["offline-files"] ? data.value["offline-files"] : {};
      params["deleted-records"] = data.value["deleted-records"] ? data.value["deleted-records"] : {};
      params["edited-images"] = data.value["edited-images"] ? data.value["edited-images"] : {};


      var isTaskForm = options.formProperties ? options.formProperties.isTaskForm : undefined;
      var hasSyncErrors = options.hasSyncError === true;
      if (isTaskForm === true && options.online === true && !hasSyncErrors) {
        // This means that a sync was performed, therefore we must trust the server
        return;
      }
      var storedDataRows = data.value.rows || [];

      params.data.records = storedDataRows.length;
      params.data.total = 1;
      params.data.page = 1;

      var lengthColumns = params.data.columns.length;
      var tmpEditAndVisible = [];
      var lengthRows = storedDataRows.length;
      params.data.rows = [];
      for (var row = 0; row < lengthRows; row++) {
        // Add random value
        var details = storedDataRows[row][storedDataRows[row].length - 1] && storedDataRows[row][storedDataRows[row].length - 1].idRow ? storedDataRows[row][storedDataRows[row].length - 1] : undefined;
        var idRow = details ? details.idRow : bizagi.util.randomNumber();

        var tmpElement = [];
        var tempRow = [];
        tmpElement[0] = idRow;

        for (var col = 0; col < lengthColumns; col++) {
          var index = -1;
          if (storedDataRows[row][col]) {
            if (storedDataRows[row][col].xpath) {
              index = params.data.columns.indexOf(storedDataRows[row][col].xpath);
              tempRow[index] = storedDataRows[row][col];
            } else
            if (!storedDataRows[row][col].changes) {
              tempRow[col] = storedDataRows[row][col];
            }
          }
          tempRow[col] = tempRow[col] != undefined ? tempRow[col] : null;
          tmpElement[tmpElement.length] = "true";
        }
        var mergedRow = $.merge([idRow], tempRow);
        params.data.rows.push(mergedRow);
        tmpEditAndVisible[tmpEditAndVisible.length] = tmpElement;
      }

      params.data.editable = tmpEditAndVisible;
      params.data.visible = tmpEditAndVisible;
      return;
    }

    // No type supported
    try {
      params.value = data.value;
    } catch (e) {
    }

    bizagi.log(type + " not supported in render factory", data, "error");
    return null;
  },

