/*
*   Name: Bizagi Rendering Services
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This class will provide a facade to access to workportal REST services
*/

bizagi.render.services.service.extend("bizagi.render.services.service", {},
{
  /* 
   *   Constructor
   */
  init: function init(params) {
    // Call base
    var self = this;

    self._super(params);
    self.database = params.database;
    self.gridService = new bizagi.grid.services.service({
      renderDataService: this,
      database: params.database });

    self.fileService = new bizagi.file.services.service({
      renderDataService: this,
      database: params.database });

    self.syncService = new bizagi.sync.services.service({
      renderDataService: this,
      database: params.database });

    self.formService = new bizagi.form.services.service({
      renderDataService: this,
      database: params.database });

  },

  getFileService: function getFileService() {
    var self = this;
    return self.fileService;
  },

  getSyncService: function getSyncService() {
    var self = this;
    return self.syncService;
  },

  getGridService: function getGridService() {
    var self = this;
    return self.gridService;
  },

  getFormService: function getFormService() {
    var self = this;
    return self.formService;
  },

  getFormData: function getFormData(params) {
    var self = this;

    params = params || {};

    if (!params.isOfflineForm) {
      return self._super(params);
    } else {
      if (params.workitemstate == "inactive" && params.online && params.alreadyMarkedAsActive !== true) {
        // Always call the backend service in order to mark the activity as opened  
        self._super(params);
      }
      if (bizagi.util.isNativePluginSupported() && typeof bizagiapp.getNativeFormData !== "undefined") {
        return self.getFormDataFromNative(params).
        then(function (result) {
          return self.database.mergeForm(result[1], result[0].data, { online: params.online });
        });

      } else {
        if (params.offlineType === 'task') {
          //It is not a start event
          if (params.isEdited || !params.isOpen) {
            var def = new $.Deferred();
            $.when(self.database.getTaskData(params)).then(function (formData) {
              var syncStatus = params.syncError || formData.syncStatus;
              var hasSyncError = params.hasSyncError || !bizagi.util.isEmpty(syncStatus);
              var formPromise = self.database.getTaskForm(params);
              $.when(self.database.mergeDataDocument(formData.data, formPromise)).done(function (complexData) {
                var object = {
                  data: complexData,
                  isOpen: params.isOpen };

                self.database.mergeForm(object, formPromise, { online: params.online, hasSyncError: hasSyncError }).then(function (mergedForm) {
                  def.resolve(mergedForm);
                });
              });

            });
            return def.promise();
          } else

          return self.database.getTaskForm(params);
        } else
        {
          return $.when(self.database.getCase(params.idCase)).
          then(function (response) {
            var idWfClass = $.trim(response.idWfClass.toString());
            return self.database.mergeForm(response, self.database.getFormData(idWfClass), { online: params.online });
          });
        }
      }
    }
  },

  getOfflineImage: function getOfflineImage(params, fileType) {
    if (params.offlineType === "task")
    return this.database.getActivityImage(params, fileType);else

    return this.database.getStartDataImage(params, fileType);
  },

  getOfflineActivityDataImage: function getOfflineActivityDataImage(params, fileFormat) {
    return this.database.getActivityDataImage(params, fileFormat);
  },

  saveOfflineImage: function saveOfflineImage(params, image) {
    if (params.operationType === "synchronization")
    return this.database.saveActivityFormImage(params, image);else
    if (params.operationType === "edition") {
      if (params.offlineType === "task")
      return this.database.saveActivityDataImage(params, image);else

      return this.database.saveStartDataImage(params, image);
    } else

    return null;
  },

  saveOfflineFile: function saveOfflineFile(params, file, mimeType) {
    if (params.operationType === "synchronization")
    return this.database.saveActivityFormFile(params, file, mimeType);else
    if (params.operationType === "edition") {
      if (params.offlineType === "task")
      return this.database.saveActivityDataFile(params, file, mimeType);else

      return this.database.saveStartDataFile(params, file, mimeType);
    } else

    return null;
  },

  getOfflineFile: function getOfflineFile(params, fileType, mimeType) {
    if (params.offlineType === "task")
    return this.database.getActivityFile(params, fileType, mimeType);else

    return this.database.getStartDataFile(params, fileType, mimeType);
  },

  getOfflineActivityDataFile: function getOfflineActivityDataFile(params, fileType, mimeType) {
    return this.database.getActivityDataFile(params, fileType, mimeType);
  },

  getFormDataFromNative: function getFormDataFromNative(params) {
    var def = new $.Deferred();

    bizagiapp.getNativeFormData(params, function (form) {

      bizagiapp.getCase(params.idCase, function (caseInfo) {

        var result = [JSON.parse(form), JSON.parse(caseInfo)];

        def.resolve(result);

      }, function (error) {

        def.reject(error);

      });


    }, function (error) {
      def.reject(error);
    });

    return def.promise();
  },

  submitData: function submitData(params) {
    var self = this;

    params = params || {};

    if (!params.isOfflineForm) {
      return self._super(params);
    } else {

      var data = self.resolveData(params.data || {}, params.xpathContext);
      var action = (params.action || "").toLowerCase();
      // current action (save, next or next without validations)
      if (action != '' && (action == 'save' || action == 'next' || action == 'nextwithoutvalidations' || action == 'submitdata')) {
        data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + "action"] = action;
      }

      if (bizagi.util.isNativePluginSupported() && typeof bizagiapp.getNativeFormData !== "undefined") {
        return self.saveNativeFormInfo(params, data);
      }

      if (params.offlineType === 'task') {
        self.sanityDataForSaveInDB(data);

        return self.database.saveTaskData(params, data);
      } else
      {
        return self.database.saveStartData(params, data);
      }
    }
  },

  sanityDataForSaveInDB: function sanityDataForSaveInDB(data) {
    delete data.deferreds;
  },

  saveNativeFormInfo: function saveNativeFormInfo(params, data) {
    var self = this;

    var def = new $.Deferred();

    self.getFormDataFromNative(params).then(function (result) {
      return self.database.nativesaveStartData(data, result[1], result[0].data).
      done(function (doc) {
        bizagiapp.saveFormData(params, doc);
        def.resolve(doc);
      });
    });

    return def.promise();
  },


  removeStartCaseSyncError: function removeStartCaseSyncError(params) {
    var self = this;
    var updatedStartCase;
    return self.database.getCase(params.idCase).
    then(function (startCase) {
      startCase.isOpen = true;
      delete startCase.syncDetails;
      delete startCase.syncStatus;
      updatedStartCase = startCase;
      return self.database.saveStartCase(params.idCase, startCase);
    }).
    then(function () {
      return updatedStartCase;
    });
  },

  removeTaskCaseSyncError: function removeTaskCaseSyncError(params) {
    var self = this;
    var offlineId = params.idCase + "-" + params.idWorkitem;
    var updatedTaskCase;
    return self.database.getTask(offlineId).
    then(function (taskCase) {
      taskCase.isOpen = true;
      taskCase.isEdited = true;
      delete taskCase.syncDetails;
      delete taskCase.syncStatus;
      updatedTaskCase = taskCase;
      return self.database.saveTask(offlineId, taskCase);
    }).
    then(function () {
      return self.database.getTaskData(params);
    }).
    then(function (taskData) {
      if (taskData && taskData.data != undefined) {
        taskData.data.h_action = "";
        delete taskData.data.retry_h_actions;
        delete taskData.data.actions;
      }
      return self.database.saveTaskData(params, taskData.data);
    }).
    then(function () {
      return updatedTaskCase;
    });
  },

  parseServiceError: function parseServiceError(callbackArguments) {
    var error = {};
    var stringError;
    if (callbackArguments == undefined) {
      return;
    }
    if (callbackArguments.responseText) {
      stringError = callbackArguments.responseText;
    } else
    if (callbackArguments.length == 1 && !callbackArguments[0].responseText) {
      error = callbackArguments[0];
    } else
    if (callbackArguments.length > 0 && callbackArguments[0] && callbackArguments[0].responseText) {
      stringError = callbackArguments[0].responseText;
    } else if (callbackArguments.length == 3) {
      var internalError = callbackArguments[2];
      while (internalError != undefined && internalError.error) {
        internalError = internalError.error;
      };
      if (internalError.errorType) {
        error = internalError;
      } else
      {
        error.type = "error";
        error.errorType = callbackArguments[1];
        error.message = callbackArguments[2];
      }
    } else
    {
      try {
        stringError = JSON.stringify(callbackArguments);
      } catch (parsingError) {}
    }

    if (stringError != undefined) {
      try {
        error = JSON.parse(stringError);
      } catch (parsingError) {
        error.type = "error";
        error.errorType = "parsingError";
        error.message = "Error parsing the server response: " + stringError;
      }
    }
    return error;
  } });