/**
 *   Name:   Bizagi diagnostics service
 *   Author: Bryan Alejandro Cubillos Prieto
 *   Date:   09/25/2020
 *   Comments:
 *   =>      This is a new service base for Bizagi diagnostics, required for centralized behavior
 */

bizagi.workportal.services.service.extend("bizagi.workportal.services.service", {

  /*************************************************************************************************
                                                                                   *          COMPONENT LOCAL DATA
                                                                                   *************************************************************************************************/
  DIAGNOSTICS_COMPONENT_NAME: 'bizagi.workportal.services.service.diagnostics.error.observer',
  DIAGNOSTICS_WAIT_APPLICATION_STARTED: 45000 },
{

  /*************************************************************************************************
   *          CONSTRUCTOR
   *************************************************************************************************/
  init: function init(params) {

    // Call base
    var self = this;
    self._super(params);

    // flag
    self.diagnosticsServiceEnabled = bizagi.util.isMobileErrorTracesEnabled();
    if (self.diagnosticsServiceEnabled) {

      // Behavior
      self.configureDiagnosticsErrorObserverHandlers();
    }
  },

  /*************************************************************************************************
      *          SERVICE METHODS
      *************************************************************************************************/
  configureDiagnosticsErrorObserverHandlers: function configureDiagnosticsErrorObserverHandlers() {
    var self = this;

    // for catch any error in jQuery
    window.onerror = function (message, source, line, col, error) {
      self.sendDiagnosticsError(error, message, source, line);
    };

    // for read any error in ajax
    setTimeout(function () {
      self.getErrorsInAjaxImplementation();
    }, self.Class.DIAGNOSTICS_WAIT_APPLICATION_STARTED);
  },

  sendDiagnosticsError: function sendDiagnosticsError(error, message, source, line) {
    var self = this;
    var dataserviceInstance = self.getDataservice();

    // for collect all error when application don't already start
    if (dataserviceInstance && dataserviceInstance.diagnosticstErrorsDb != undefined) {
      dataserviceInstance.addDiagnosticsError(error, message, source, line);
    }
  },

  /*************************************************************************************************
      *          SERVICE FUNCTIONS
      *************************************************************************************************/
  getDataservice: function getDataservice() {
    return bizagiWorkportal.deviceFactory.workportal.dataService;
  },

  getJqueryAjax: function getJqueryAjax() {
    return bizagi.services.ajax;
  },

  getErrorsInAjaxImplementation: function getErrorsInAjaxImplementation() {
    var self = this;
    var ajaxInstance = self.getJqueryAjax();

    if (ajaxInstance && ajaxInstance.mobileHttpErrorsQueue && ajaxInstance.mobileHttpErrorsQueue.length > 0) {
      for (var i = 0; i < ajaxInstance.mobileHttpErrorsQueue.length; i++) {
        self.sendDiagnosticsError(ajaxInstance.mobileHttpErrorsQueue[i]);
      }
    }
  } });