  /**
      * get last process inbox workflow
      * @return {workflow} 
      */
  getLastProcessInboxWorkflow: function getLastProcessInboxWorkflow() {
    var self = this;
    var defer = new $.Deferred();

    // localstorage
    var lastProcessInbox = bizagi.util.getLastProcessInboxProcessInfo();

    if (bizagi.util.validateFeatureSupport("defaultInboxEnabled") == false || bizagi.util.isMobileInboxByProcessSupported() == false) {
      if (lastProcessInbox.idWorkflow != -1)
      bizagi.util.setLastProcessInbox(lastProcessInbox.processInfoName, lastProcessInbox.idWorkflow);
      defer.resolve(lastProcessInbox);
    } else
    {
      // Last Open Inbox -> Default Open Inbox -> All processes
      $.when(
      self.getUserWorkportalPreferences(),
      self.getAllProcesses({
        taskState: "all",
        onlyFavorites: false,
        inputtray: "inbox",
        skipAggrupate: true,
        mobileDevice: true }),

      self.getAllSmartInbox()).
      then(function (userPreferences, processesData, smartinboxes) {
        // Preferences result
        var idWorkFlowResult = lastProcessInbox.idWorkflow,
        smartInboxId = lastProcessInbox.smartInboxId,
        _preferDefaultOpenInbox = parseInt(userPreferences.defaultOpenInboxWorkflowId),
        _preferDefaultOpenSmartInbox = userPreferences.defaultOpenInboxSmartInboxId,
        _hasPreferDefaultOpenInbox = _preferDefaultOpenInbox > 0,
        _hasPreferDefaultOpenSmartInbox = !bizagi.util.isEmpty(_preferDefaultOpenSmartInbox);

        // First way ask by last Open Inbox
        if (idWorkFlowResult == -1 && _hasPreferDefaultOpenInbox && bizagi.util.isEmpty(smartInboxId)) {
          idWorkFlowResult = _preferDefaultOpenInbox;
          smartInboxId = null;
        } else if (bizagi.util.isEmpty(smartInboxId) && _hasPreferDefaultOpenSmartInbox && idWorkFlowResult == -1) {
          smartInboxId = _preferDefaultOpenSmartInbox;
          idWorkFlowResult = -1;
        }

        if (processesData && processesData.workflows && idWorkFlowResult != -1)
        {
          // search processName by workflowId
          var workflowFound = $.first(processesData.workflows.workflow, function (workflow) {return workflow.idworkflow == idWorkFlowResult;});
          if (workflowFound) {
            bizagi.util.setLastProcessInbox(workflowFound.name, idWorkFlowResult);
          } else {
            bizagi.util.setLastProcessInbox(null, -1);
          }

        } else if (smartinboxes && smartinboxes.length > 0 && !bizagi.util.isEmpty(smartInboxId)) {
          // search smartinbox by id
          var smartinboxFound = $.first(smartinboxes, function (smartinbox) {return smartinbox.id == smartInboxId;});
          if (smartinboxFound) {
            bizagi.util.setLastProcessInbox(smartinboxFound.name, -1, smartInboxId);
          } else {
            bizagi.util.setLastProcessInbox(null, -1);
          }
        }

        // get info from local storage
        defer.resolve(bizagi.util.getLastProcessInboxProcessInfo());

      }).fail(function () {
        defer.reject('Error in bizagi.workportal.services.service.getLastProcessInboxWorkflow');
      });

    }

    return defer.promise();
  },

