  /**
      * Logout Service
      */
  logout: function logout() {
    var self = this;
    // Remove session storage of authentication data
    sessionStorage.removeItem('bizagiAuthentication');

    //Call to the restfull service for domain list
    try {
      var javaLogout = $.create(self.serviceLocator.getUrl('logout'));

      $.when(javaLogout).done(function () {
        location.reload();
      }).fail(function () {
        bizagi.log('Error logging out');
      });
    } catch (e) {}
  },

  /**
      * Logout from different types of services
      * This method can reload the page after calling the logout service required
      */
  logoutMobile: function logoutMobile() {
    var self = this;
    var logoutData = { sourceLogout: "user" };

    // Remove session storage of authentication data
    sessionStorage.removeItem("bizagiAuthentication");

    // Remove last process inbox
    bizagi.util.removeLastProcessInbox();

    // Hybrid will use normal system
    if (bizagi.util.isCordovaSupported()) {
      var url = self.serviceLocator.getUrl("logout");
      return $.create(url);
    } else {
      // Call to the restfull service for domain list
      try {

        $.when(self.getConfiguration()).done(function (data) {
          if (data.redirectErrorPage) {
            window.location.href = data.redirectErrorPage;
            return;
          }

          var authenticationType = data.authenticationType;
          switch (authenticationType) {
            case "Federate":
              var logOffURL = data.logOffURL;
              var federationLogoutBindingProtocol = data.federationLogoutBindingProtocol;
              var authenticationSubType = data.authenticationSubType;
              if (authenticationSubType === 'WSFederated') {
                $.when($.create(self.serviceLocator.getUrl("logout"), logoutData)).done(function () {
                  location.href = logOffURL;
                }).fail(function () {
                  bizagi.log("Error logging out");
                }).always(function () {
                  location.href = data.logOffURL;
                });
              } else
              if (authenticationSubType === 'Saml') {
                var param = {};
                $.when($.read(self.serviceLocator.getUrl("FederateLogoffUrl"), param)).done(function (res) {
                  if (res.federateLogoffUrl) {
                    logOffURL = res.federateLogoffUrl;
                  }

                  $.when($.create(self.serviceLocator.getUrl("logout"), logoutData)).done(function () {
                    if (federationLogoutBindingProtocol === 'POST') {
                      window.document.open('text/html');
                      window.document.write(logOffURL);
                      window.document.close();
                    } else
                    {
                      window.location.href = logOffURL;
                    }
                  }).fail(function () {
                    bizagi.log("Error logging out");
                  }).always(function () {
                  });

                }).fail(function () {
                  bizagi.log("Error logging out");
                }).always(function () {
                });
              }
              break;
            case "SAML":
              loader.nativeAjax(loader.getPathUrl(self.serviceLocator.getUrl("logout"), logoutData),
              function (response) {
                var jsonResponse = JSON.parse(response.responseText);

                if (jsonResponse.logout === true) {
                  if (typeof jsonResponse.samlUrlRedirect != "undefined") {
                    var url = decodeURI(jsonResponse.samlUrlRedirect);
                    var iframe = document.createElement("iframe");

                    document.getElementsByTagName("body")[0].appendChild(iframe);

                    $(iframe).on("load", function () {
                      var iframeContent = bizagi.util.isIE8() ?
                      $(this.contentWindow.document.body) :
                      $(this.contentWindow.eval("document.body"));
                      var rawResponse = iframeContent.text();

                      location.reload();
                    });

                    iframe.src = url;

                  } else {
                    location.reload();
                  }
                } else {
                  bizagi.log("Error logging out");
                }
              });
              break;
            case "OAuth2":
              $.when($.create(self.serviceLocator.getUrl("logout"), logoutData)).done(function () {
                $.when($.read(self.serviceLocator.getUrl("oauth2AuthenticationConfig"))).
                done(function (response) {
                  if (response.logoutUrl && response.logoutUrl != "") {
                    var logoutUrl = response.logoutUrl;
                    var callbackUrl = response.homeUrl;

                    logoutUrl = logoutUrl.replace(/post_logout_redirect_uri=[^&]*/,
                    "post_logout_redirect_uri=" + callbackUrl);
                    location.href = logoutUrl;
                  } else {
                    location.reload();
                  }

                }).fail(function () {
                  bizagi.log("Error logging out");
                });
              }).fail(function () {
                bizagi.log("Error logging out");
              });
              break;
            default:
              //Normal
              $.when($.create(self.serviceLocator.getUrl("logout"), logoutData)).
              done(function () {
                window.location = bizagi.services.ajax.loginPage;
              }).fail(function () {
                bizagi.log("Error logging out");
              });
              break;}

        }).fail(function (message) {
          bizagi.log(message);
        });
      } catch (e) {
        bizagi.log(e);
      }
    }
  },

