  /**
      * Method that calls the activities endpoint and handles sorting and filtering
      */
  getMobileActivities: function getMobileActivities(params) {
    var self = this;
    var url = self.serviceLocator.getUrl("case-handler-getActivityList");

    var data = params || {};
    // information for sort
    var sortData = params.sortData;
    if (sortData) {
      if (bizagi.util.isMobileInboxByProcessSupported() && sortData.sortOrder && sortData.sortDirection && sortData.sortFieldName) {
        data.orderFieldName = sortData.sortFieldName;
        data.orderType = sortData.sortDirection == 'asc' ? 0 : 1;
        data.order = sortData.sortOrder;
      } else
      {
        delete data.sortData;
      }
    }
    if (typeof params.radNumber !== "undefined") {
      data.radNumber = params.radNumber;
    }
    if (params.idWfClass != -1) {
      data.idWfClass = params.idWfClass;
    }
    data.mobileDevice = bizagi.util.isMobileDevice();
    data.page = data.page || 1;
    data.pageSize = data.pageSize || 10;
    if (data.idWfClass != undefined && data.idWfClass < 0) {
      delete data.idWfClass;
    }

    if (params.smartInboxId) {
      data.smartInboxGuid = params.smartInboxId;
    }

    if (params.liveFilterData) {
      data.smartInboxFilter = self.encodeLiveFilter(params.liveFilterData);
      delete params.liveFilterData;
    }

    var ajaxParams = {
      url: url,
      data: data,
      type: "GET",
      dataType: "json" };

    if (params.avoidLoader) {
      ajaxParams.avoidLoader = true;
      delete data.avoidLoader;
    }


    var serverResponse;
    return $.ajax(ajaxParams).then(function (response) {
      bizagi.util.setLastProcessInboxProcessColumns(response.headers);
      serverResponse = response;
      return params.includeSyncErrors && params.inputtray == "inbox" ? self.getInboxSyncErrors() : undefined;
    }).
    then(function (localSyncErrors) {
      if (localSyncErrors != undefined) {
        serverResponse.syncErrors = localSyncErrors;
      }
      return serverResponse;
    });

  },

  /**
      * Encode json filter to base64
      * @param {json} filter 
      */
