  /**
      * Include default all processes item in server resposne
      */
  includeAllProcessesItem: function includeAllProcessesItem(response) {
    // all Process - no filter
    var allProcessItem = {
      idworkflow: "-1",
      guidWFClass: "-1",
      name: bizagi.localization.getResource("workportal-widget-inbox-all-processes"),
      path: "App/",
      count: 0,
      isFavorite: "false",
      icon: "",
      category: "Processes",
      guidFavorite: "",
      orderValue: String.fromCharCode(0) };


    if (response.workflows) {
      // each for count all cases or activities
      $.each(response.workflows.workflow, function (i, workflow) {
        response.workflows.workflow[i].orderValue = response.workflows.workflow[i].name;
        allProcessItem.count += Number(workflow.count);
      });
      // Set first element in the list
      response.workflows.workflow.unshift(allProcessItem);
    };
  },

