/*
*   Name: Bizagi Rendering Services
*   Author: Bizagi Mobile Team
*   Comments:
*   -   This class will provide a facade to access to workportal REST services
*/
bizagi.form = bizagi.form || {};
bizagi.form.services = bizagi.form.services || {};
bizagi.form.services.service = bizagi.form.services.service || {};
$.Class.extend("bizagi.form.services.service", {
  // STATIC METHODS
},

{
  // INSTANCE METHODS
  init: function init(params) {
    var self = this;
    self.renderDataService = params.renderDataService;
    self.database = params.database;
    self.fileService = self.renderDataService.getFileService();
  },

  getServerTaskForm: function getServerTaskForm(params) {
    var self = this;
    var form;
    var hasEntryPipeline;
    var wasPipelineExecuted;
    params.downloadOfflineForm = true;
    var service = params.bulkDownload ? self.renderDataService.multiactionWithLimit() : self.renderDataService;
    return service.getFormData(params).
    then(function (response) {
      return self.executeOfflineEntryPipelineAndReloadForm(response, params);
    }).
    then(function (pipelineExecutionResponse) {
      form = pipelineExecutionResponse.form;
      hasEntryPipeline = pipelineExecutionResponse.hasEntryPipeline === true;
      wasPipelineExecuted = pipelineExecutionResponse.wasPipelineExecuted === true;
      return self.getFileAttachmentsFromLocalTaskForm(params);
    }).
    then(function (localFiles) {
      if (form.errorType) return $.Deferred().reject(form).promise();
      if (params.deleteNextButton) form = self.database.deleteNextButton(form);

      form.dataChangeset = params.dataChangeset;
      params.localFiles = localFiles;

      var result = self.fileService.downloadFormFiles(form, params);

      var filePromises = result.promises;
      var imagePromises = self.fileService.downloadFormImages(form, params);
      var downloadAttachmentsPromises = Array.prototype.push.apply(filePromises, imagePromises);

      if (params.bulkDownload && downloadAttachmentsPromises.length > 0) {

        // When downloading offline forms in bulk, wait for data promises
        return $.when.apply($, downloadAttachmentsPromises).
        then(function () {
          return self.mergeForm(form, result.data);
        });

      }

      // Single offline form, don't wait for files
      return self.mergeForm(form, result.data);
    }).
    then(function (resolvedForm) {
      return {
        form: resolvedForm,
        hasEntryPipeline: hasEntryPipeline,
        wasPipelineExecuted: wasPipelineExecuted };

    }).
    fail(function () {
      bizagi.util.console.error("Error downloading task form", params);
    });
  },

  executeOfflineEntryPipeline: function executeOfflineEntryPipeline(formData) {
    var self = this;
    var formProperties = formData.form ? formData.form.properties : {};
    var shouldExecutePipeline = self.hasFormEntryPipeline(formData);

    if (!shouldExecutePipeline) {
      return $.Deferred().resolve({ hasEntryPipeline: false, wasPipelineExecuted: false }).promise();
    }

    return $.when(self.renderDataService.executePipeline({
      pipeline: formProperties.loadExecution,
      idPageCache: formData.form.pageCacheId })).

    then(function () {
      return { hasEntryPipeline: true, wasPipelineExecuted: true };
    }, function () {
      return $.Deferred().resolve({ hasEntryPipeline: true, wasPipelineExecuted: false }).promise();
    });
  },

  executeOfflineEntryPipelineAndReloadForm: function executeOfflineEntryPipelineAndReloadForm(data, params) {
    var self = this;

    if (!params.executeOfflineEntryPipeline) return { form: data, wasPipelineExecuted: false };

    // Execute pipeline, then reload form
    return self.executeOfflineEntryPipeline(data).
    then(function (response) {
      hasEntryPipeline = response.hasEntryPipeline;
      wasPipelineExecuted = response.wasPipelineExecuted;

      if (hasEntryPipeline && wasPipelineExecuted) {
        return self.renderDataService.getFormData(params).
        then(function (getFormDataResponse) {
          return {
            form: getFormDataResponse,
            hasEntryPipeline: hasEntryPipeline,
            wasPipelineExecuted: wasPipelineExecuted };

        });
      }
      return {
        form: data,
        hasEntryPipeline: hasEntryPipeline,
        wasPipelineExecuted: wasPipelineExecuted };

    });
  },

  getFileAttachmentsFromLocalTaskForm: function getFileAttachmentsFromLocalTaskForm(params) {
    var self = this;
    return self.database.getFileAttachmentsFromLocalTaskForm(params).
    then(function (localAttachments) {
      return localAttachments;
    }, function () {
      return {};
    });
  },

  mergeForm: function mergeForm(form, data) {
    var self = this;
    return self.database.mergeDataDocument(data, form).
    then(function (complexData) {
      var object = { data: data };
      object.data = complexData;
      object.isOpen = true;

      return self.database.mergeForm(object, form, { online: true });
    });
  },

  downloadOfflineStartForm: function downloadOfflineStartForm(params) {
    var self = this;
    params.action = "LOADSTARTFORM";
    params.downloadOfflineForm = true;

    return self.renderDataService.multiactionWithLimit().getFormData(params).
    then(function (serverResponse) {
      if (serverResponse.errorType) return $.Deferred().reject(serverResponse).promise();
      if (!serverResponse.form) return $.Deferred().reject(serverResponse).promise();

      serverResponse.form.properties.isTaskForm = false;
      if (serverResponse.form.buttons === undefined || Array.isArray(serverResponse.form.buttons) && serverResponse.form.buttons.length < 2)
      serverResponse.form.buttons = self.addFormDefaultButtons();;
      serverResponse.lastSynchronization = Date.now();

      return self.database.saveStartForm(params, serverResponse);
    });
  },

  downloadOfflineTaskForm: function downloadOfflineTaskForm(params) {
    var self = this;
    var hasEntryPipeline;
    var wasPipelineExecuted;
    params.deleteNextButton = false;

    return self.getServerTaskForm({
      idCase: params.idCase,
      idWorkitem: params.idWorkitem,
      executeOfflineEntryPipeline: params.executeOfflineEntryPipeline === true,
      bulkDownload: params.bulkDownload }).

    then(function (response) {

      var form = response.form;
      hasEntryPipeline = response.hasEntryPipeline;
      wasPipelineExecuted = response.wasPipelineExecuted;
      form.form.properties.isTaskForm = true;

      if (form.form.buttons === undefined || Array.isArray(form.form.buttons) && form.form.buttons.length == 0)
      form.form.buttons = self.addFormDefaultButtons();

      return self.database.saveTaskForm(params, form);
    }).
    then(function () {

      if (!params.isSingleSync) {
        var formNotificationParams = $.extend({}, params);
        formNotificationParams.type = "download-form-success";
        $(document).trigger("offline-notify-form-sync-event", formNotificationParams);
      }

      return {
        wasFormDownloaded: true,
        hasEntryPipeline: hasEntryPipeline,
        wasPipelineExecuted: wasPipelineExecuted };

    });
  },

  /*************************************************************************************************
      *          UTIL METHODS
      *************************************************************************************************/

  addFormDefaultButtons: function addFormDefaultButtons() {
    return [
    {
      button: {
        properties: {
          actions: [
          "submitData",
          "refresh"],

          caption: "render-form-button-save",
          id: "ec0a5a02-0f81-44a7-8d64-ac090a01d61b" } } },



    {
      button: {
        properties: {
          actions: [
          "validate",
          "submitData",
          "next",
          "routing"],

          caption: "render-form-button-next",
          id: "ba81c7af-0d83-4139-879d-b040f0f81bd7" } } }];




  },

  // TODO: Check if there is another way to check this without invoking 'renderDataService.getFormData'
  verifyFormEntryPipeline: function verifyFormEntryPipeline(params) {
    var self = this;
    return self.renderDataService.getFormData(params).
    then(function (formData) {
      return self.hasFormEntryPipeline(formData);
    }).
    fail(function (err) {
      bizagi.util.console.error("verifyFormEntryPipeline => getFormData error", err);
      return false;
    });
  },

  hasFormEntryPipeline: function hasFormEntryPipeline(formData) {
    var formProperties = formData.form ? formData.form.properties : {};
    var hasEntryPipeline = bizagi.util.parseBoolean(formProperties.hasEntryPipeline) === true &&
    !bizagi.util.isEmpty(formProperties.loadExecution);
    return hasEntryPipeline;
  } });